<template>
  <panel :index="index" :query="query">
    <div class="d-panel-header">
      <img class="img-panel-header" data-src="query.img" class="lazy">
    </div>
    <div class="list-block">
      <ul class="media-list" v-tap="closePanel">
        <li class="item-link link" v-for="part in arr" v-link.bubbles.clear="{comp:'map'}">
          <div class="item-content">
            <div class="item-media">
              <img :src="part.img" style="width: 30px; height: 30px;">
            </div>
            <div class="item-inner" v-text='part'></div>
          </div>
        </li>
      </ul>
    </div>
  </panel>
</template>
<script>
import F7 from './../Framework7'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      title: '地图',
      arr: [0, 1, 2, 3, 4, 5]
    }
  },
  methods: {
    closePanel: function () {
      F7.operates.$panel.close()
    }
  },
  ready: function () {}
}
</script>
<style>
.img-panel-header {
  width: 150px;
  height: 150px;
  border-radius: 100%;
  border: 5px solid #78b5f9;
}

.d-panel-header {
  width: 160px;
  width: 160px;
  margin: 15px auto;
}
</style>



