<template>
  <div class="card facebook-card status-card">
    <div class="card-header">
      <div class="facebook-avatar" v-if="item.user.head">
        <img :src="item.user.head" width="34" height="34" v-tap="showUserHome()">
      </div>
      <div class="facebook-name" v-if="item.user.name" v-text="item.user.name"></div>
      <div class="facebook-date" :style="{'margin-left':!item.user.name?'0':''}" v-dateformat="item.create_at"></div>
    </div>
    <div class="card-content">
      <div class="card-content-inner" v-if="item.content || item.photo.length>1">
        <p v-text="item.content" v-if="item.content"></p>
        <div class="row" v-if="item.photo.length>1">
          <img :src="staticUrl+part+'-thumbmulti'" class="col-33" v-for="part in item.photo" v-tap='showPhoto($index)' />
        </div>
      </div>
      <img :src="staticUrl+item.photo[0]+'-thumbsingle'" v-if="item.photo.length==1 " width="100%" v-tap='showPhoto()'>
    </div>
    <div class="card-footer">
      <a class="link" :class="{'color-blue':item.isLike}" v-tap="like()">点赞<span style="margin-left: 10px " v-text='item.likeCount'></span></a>
      <a class="link" v-tap="comment() ">评论<span style="margin-left: 10px " v-text='item.commentCount'></span></a>
    </div>
  </div>
</template>
<script>
export default {
  props: ['item'],
  data: function () {
    return {}
  },
  methods: {
    showPhoto: function () {
      this.$root.bodyChildren.push({
        comp: 'Gallery',
        query: this.item.photo
      })
    },
    showUserHome: function () {
      this.$v7.$router.go('UserHome', this.item.user.id)
    },
    like: function () {
      var link = 'like/'
      if (this.item.isLike) {
        link += 'destroy'
      } else {
        link += 'create'
      }
      this.$http.get(this.apiUrl + link, {
        id: this.item.id
      }).then(function (response) {
        this.item.likeCount = response.data.likeCount
        this.item.isLike = response.data.isLike
        if (response.data.isLike) {
          window.vm.notify('点赞成功')
        } else {
          window.vm.notify('取消成功')
        }
      }, function (response) {
        window.vm.$v7.$notify.add({
          title: '去冒泡',
          message: '点赞出错:' + response.data.message
        })
      })
    },
    comment: function (id) {
      window.vm.$v7.$comment.show()
      window.vm.$v7.$comment.comment({
        commentId: this.item.id,
        hideComment: false
      })
    }
  }
}
</script>
<style>
.status-card .row {
  justify-content: flex-start;
  margin: -8px -4px;
}

.status-card .row .col-33 {
  width: calc((100% - 24px) / 3);
  margin: 4px;
}

.card-header-pic .card-header {
  height: 40vw;
  background-size: cover;
  background-position: center;
}

.facebook-card .card-header {
  display: block;
  padding: 10px;
}

.facebook-card .facebook-avatar {
  float: left;
}

.facebook-card .facebook-name {
  margin-left: 44px;
  font-size: 14px;
  font-weight: 500;
}

.facebook-card .facebook-date {
  margin-left: 44px;
  font-size: 13px;
  color: #8e8e93;
}

.facebook-card .card-footer {
  background: #fafafa;
}

.facebook-card .card-footer a {
  color: #81848b;
  font-weight: 500;
}

.facebook-card .card-content img {
  display: block;
}

.facebook-card .card-content-inner {
  padding: 15px 10px;
}
</style>



