<template>
  <div class="toolbar messagebar comment" :class="{'toolbar-hidden':status, 'toolbar-hiding':!status}" style="position: fixed;">
    <div class="toolbar-inner">
      <textarea placeholder="点击这里评论哦..." v-model="commentData.content"></textarea>
      <a href="#" class="link color-blue" v-tap="sendComment()">评论</a>
    </div>
  </div>
</template>
<script>
export default {
  props: ['status', 'item', 'list'],
  data: function () {
    return {
      commentData: {
        content: ''
      },
      commentid: ''
    }
  },
  methods: {
    clearComment: function (index) {
      this.commentid = ''
      this.commentData.content = ''
    },
    sendComment: function () {
      var commentid = this.commentid || this.item.id
      if (this.commentData.content === '') {
        window.vm.$v7.$modal.show({
          title: 'T-T 请说点什么呗',
          buttons: [{
            text: '确定'
          }]
        })
        return
      }
      window.vm.$v7.$indicator.show()
      var params = {
        id: commentid,
        content: this.commentData.content,
        longitude: (Math.random() * 60 + 25).toFixed(10),
        latitude: (Math.random() * 60 + 25).toFixed(10)
      }
      this.$http.get(this.apiUrl + 'comment/publish', params).then(function (response) {
        // this.item.commentCount = response.data.commentCount
        window.vm.$v7.$indicator.hide()
        this.clearComment()
        window.vm.notify('^.^ 恭喜您刚刚给小伙伴评论了一下下')
      }, function (response) {
        window.vm.$v7.$indicator.hide()
        window.vm.$v7.$modal.show({
          title: response.data.message,
          buttons: [{
            text: '确定'
          }]
        })
      })
    }
  },
  ready: function () {
    window.vm.$v7.$comment.init(this)
  }
}
</script>
<style>
.comment {
  z-index: 13500;
}
</style>



