var compList = []
export default {
  open: function (comp, id, query) {
    compList.push(comp)
    window.vm.bodyChildren.push({
      comp: comp,
      id: id,
      query: query
    })
  },
  close: function () {
    var comp = compList.pop()
    if (comp) {
      window.vm.bodyChildren.delete(function (item) {
        return item.comp === comp
      })
    }
  }
}



