<template>
  <popup style="background:initial" :index="index" class="publish">
    <div class="card facebook-card status-card" style="min-height: calc(100% - 20px);">
      <div class="card-header" style="display: flex;">
        <button class="button button-fill" v-tap="close()">返回</button>
        <div style="text-align: center; width: 100%;" v-text="$root.user.name"></div>
        <button class="button button-fill" :class="{'bg-gray':(content == '' && gallery.length == 0) || isUploading || !hasCoordinate}" v-tap="publish()">发布</button>
      </div>
      <div class="card-content" style="border-bottom: 1px solid #f0f0f0;">
        <textarea name="content" placeholder="要不要来点什么~" v-model="content"></textarea>
      </div>
      <div class="card-content">
        <div class="list-block" style="margin: 0;">
          <ul>
            <li>
              <div class="publish-upload item-content" style="padding-left: 0;">
                <div class="item-inner" style="padding-right: 0;">
                  <div class="gallery">
                    <div class="photo-item" style="position: relative;" v-for="part in gallery">
                      <upload-item :file="part" :index="$index">
                      </upload-item>
                    </div>
                    <div class="photo-item" v-if="gallery.length<9">
                      <div class="photo">
                        <input id="fileUpload" type="file" multiple="multiple" @change="fileChange" v-el:file-upload />
                        <div class="camera">
                          <svg height="30" viewBox="0 0 24 24" width="30" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" fill="#888">
                            <defs>
                              <path d="M24 24H0V0h24v24z" id="a" />
                            </defs>
                            <clipPath id="b">
                              <use overflow="visible" xlink:href="#a" />
                            </clipPath>
                            <path clip-path="url(#b)" d="M3 4V1h2v3h3v2H5v3H3V6H0V4h3zm3 6V7h3V4h7l1.8 2H21c1 0 2 1 2 2v12c0 1-1 2-2 2H5c-1 0-2-1-2-2V10h3zm7 9c2.8 0 5-2.2 5-5s-2.2-5-5-5-5 2.2-5 5 2.2 5 5 5zm-3.2-5c0 1.8 1.4 3.2 3.2 3.2s3.2-1.4 3.2-3.2-1.4-3.2-3.2-3.2-3.2 1.4-3.2 3.2z" />
                          </svg>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="card-footer">
        <div v-tap="getLocation()">
          <svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="16px" height="16px" viewBox="0 0 48 48" enable-background="new 0 0 16 16" xml:space="preserve" fill="#888">
            <path d="M 24.00,0.00C 14.061,0.00, 6.00,8.061, 6.00,18.00s 18.00,30.00, 18.00,30.00s 18.00-20.061, 18.00-30.00S 33.939,0.00, 24.00,0.00z M 24.00,30.00c-6.627,0.00-12.00-5.373-12.00-12.00s 5.373-12.00, 12.00-12.00s 12.00,5.373, 12.00,12.00S 30.627,30.00, 24.00,30.00z M 24.00,12.00C 20.688,12.00, 18.00,14.688, 18.00,18.00s 2.688,6.00, 6.00,6.00s 6.00-2.688, 6.00-6.00 S 27.312,12.00, 24.00,12.00z"></path>
          </svg>
          <span class="location" v-text="location"></span>
        </div>
      </div>
    </div>
  </popup>
</template>
<script>
// TODO: 地理位置相关的未作审核
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      content: '',
      gallery: [],
      files: [],
      amountUploaded: 0,
      isUploading: false,
      amountUploading: 0,
      location: '获取中......',
      hasCoordinate: true, // false   以后能够正常获取地址之后,把默认值改回false
      coordinate: []
    }
  },
  events: {
    'upload': function (status, index) {
      this.gallery[index].upload = status
      this.isUploading = this.gallery.exist(function (item) {
        return item.upload === undefined
      })
    },
    'delete': function (index) {
      this.gallery.splice(index, 1)
      this.isUploading = this.gallery.exist(function (item) {
        return item.upload === undefined
      })
    }
  },
  methods: {
    fileChange: function () {
      this.files = this.$els.fileUpload.files
      this.amountUploading = this.files.length
      if (this.amountUploading + this.amountUploaded > 9) {
        window.vm.$v7.$modal.show({
          title: '提示',
          text: '您还可以上传' + (9 - this.amountUploaded) + '张图片,请删除之后再添加!',
          buttons: [{
            text: '确定'
          }]
        })
        return
      }
      this.isUploading = true
      this.gallery.push.apply(this.gallery, this.files)
    },
    getLocation: function () {
      this.location = '正在获取地理位置'
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            console.log('latitude:' + position.coords.latitude)
            console.log('longitude:' + position.coords.longitude)
            this.coordinate = {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude
            }
            this.hasCoordinate = true
              // 把this.hasCoordinate设为true前需要将坐标转换成真实地址,回填到this.location,暂时没接口
          },
          (error) => {
            if (error.code === 1) {
              this.location = '用户未许可获取位置'
            } else if (error.code === 2) {
              this.location = '该设备不支持定位功能'
            } else if (error.code === 3) {
              this.location = '网络超时'
            }
          })
      } else {
        this.location = '不支持获取地理位置'
      }
    },
    togglePublic: function () {
      this.isPublic = !this.isPublic
    },
    close: function () {
      window.history.back()
    },
    publish: function () {
      if (this.content === '' && this.gallery.length === 0) {
        window.vm.$v7.$modal.show({
          title: 'T-T 请说句话或者来张图片呗',
          buttons: [{
            text: '确定'
          }]
        })
        return
      }
      window.vm.$v7.$indicator.show()
      var params = {
        content: this.content,
        longitude: this.coordinate.longitude,
        latitude: this.coordinate.latitude,
        photo: this.gallery.map(function (item) {
          return item.key
        })
      }
      this.$http.get(this.apiUrl + 'status/publish', params).then(function (response) {
        window.vm.$v7.$indicator.hide()
        window.vm.$v7.$notify.add({
          title: '去冒泡',
          message: '恭喜您刚完成了一次喊话!赶紧去跟小伙伴们互动吧!'
        })
        this.close()
      }, function (response) {
        window.vm.$v7.$indicator.hide()
        window.vm.$v7.$modal.show({
          title: response.data.message,
          buttons: [{
            text: '确定'
          }]
        })
      })
    }
  },
  ready: function () {
    this.getLocation()
    if (this.$root.user === undefined) {
      this.$watch('$root.isLogin', function (newValue) {
        if (newValue) {
          this.userInfo = this.$root.user
        }
      })
    }
  }
}
</script>
<style>
.publish .card-header .button {
  width: 30%;
}

.ios .publish .card-header .button {
  border-radius: 2px;
}

.android .publish .card-header {
  padding-top: 10px;
  padding-bottom: 10px;
}

.publish span.location {
  margin-right: auto;
  margin-left: 10px;
  font-size: 14px;
  color: #888;
  display: inline-block;
}

.card-content textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  box-sizing: border-box;
  border: none;
  background: none;
  border-radius: 0 0 0 0;
  box-shadow: none;
  display: block;
  padding: 8px 16px;
  margin: 0;
  width: 100%;
  height: 100px;
  color: #000;
  font-size: 17px;
  font-family: inherit;
}
</style>



