<template>
  <page :index="index">
    <navbar title="用户主页"></navbar>
    <page-list url="user/show" property="status" :data.sync="data" :list.sync="list" :post-data="{id:id}">
      <div class="userhome" v-if="data">
        <div class="userhome-header color-white no-border">
          <div class="userhome-header-bg">
            <img :src="data.head" v-tap='showInfo()'>
          </div>
          <div class="userhome-header-name" v-text="data.name">
          </div>
        </div>
        <div style="padding: 16px;background-color:#FFF">
          <br>
          <p>
            <span style="margin-right: 20px" v-text='"好友："+data.relationCount'></span>
            <span v-text='"状态："+data.statusCount'>10</span>
          </p>
          <p v-text='data.desc || "这个人很懒，什么都没有留下"'>
          </p>
        </div>
      </div>
      <div class="list-block media-list" style="margin: 0">
        <status-card v-for="part in list" v-tap="showBubble(part.id)" :item="part"></status-card>
      </div>
    </page-list>
  </page>
</template>
<script>
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      data: null,
      list: []
    }
  },
  methods: {
    showInfo: function () {
      this.$v7.$router.go('UserInfo', this.data.id)
    },
    showBubble: function (id) {
      this.$v7.$router.go('Bubble', id)
    }
  }
}
</script>
<style>
.userhome {
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3);
}

.userhome .userhome-header {
  height: 44vw;
  background-size: cover;
  background-position: center;
  background-image: url(http://192.168.1.11/canvasImage.png);
  text-align: right;
}

.userhome .userhome-header img {
  width: 20vw;
  height: 20vw;
  border: 2px solid white;
  border-radius: 10px;
  top: 12vw;
}

.userhome .userhome-header-bg {
  position: absolute;
  top: 44vw;
  left: 16px;
  z-index: 1;
}

.userhome .userhome-header-name {
  position: absolute;
  right: 0;
  top: 48vw;
  padding: 0 8px;
  font-size: 5vw;
  letter-spacing: 1px;
  font-weight: bold;
  text-shadow: 0px 1px 2px rgba(255, 255, 255, 0.5);
}
</style>



