export default {
  show: function (query) {
    window.vm.bodyChildren.push({
      comp: 'Popover',
      query: query
    })
  },
  hide: function () {
    window.vm.bodyChildren.delete(function (item) {
      return item.comp === 'Popover'
    })
  }
}



