<template>
  <div class="popover" :style="'left:'+option.pop_left+'px;top:'+option.pop_top+'px;'" transition="popover">
    <div class="popover-angle" :class="{'on-top':option.anglesite === 'top','on-bottom':option.anglesite === 'bottom'}" :style="'left:'+option.ang_left+'px;'"></div>
    <div class="popover-inner">
      <div class="content-block" v-if="query.about" v-html="query.about"></div>
      <div class="list-block" v-if="query.link">
        <ul>
          <li v-for="item in query.link">
            <a v-tap="click(item)" class="list-button item-link" v-text="item.title"></a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
// import $popover from './../operates/$popover'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        pop_left: 0,
        pop_top: 0,
        ang_left: 12,
        anglesite: 'bottom'
      }
    }
  },
  methods: {
    open: function () {

    },
    close: function () {

    },
    click: function (item) {
      if (item.onClick) item.onClick()
    },
    sizePopover: function () {
      var modalPosition = 'top'
      var popoverObj = document.getElementsByClassName('popover')[0]
        // popover 的大小
      var modalWidth = popoverObj.offsetWidth
      var modalHeight = popoverObj.offsetHeight
      var modalAngleSize = 0
      var diff = 0
      var modalTop = 0
      var modalLeft = 0
      var modalAngleLeft = 10
      var angleObj = document.getElementsByClassName('popover-angle')[0]
      var windowWidth = document.body.offsetWidth
      var windowHeight = document.body.offsetHeight
      modalAngleSize = angleObj.offsetWidth / 2
        // 目标元素的大小
      var targetWidth = window.event.target.offsetWidth
      var targetHeight = window.event.target.offsetHeight
      var targetTop = window.event.target.offsetTop
      var targetLeft = window.event.target.offsetLeft

      if ((modalHeight + modalAngleSize) < targetTop) {
        // On top
        modalTop = targetTop - modalHeight - modalAngleSize
      } else if ((modalHeight + modalAngleSize) < windowHeight - targetTop - targetHeight) {
        // On bottom
        modalPosition = 'bottom'
        modalTop = targetTop + targetHeight + modalAngleSize
      }
      modalLeft = targetWidth / 2 + targetLeft - modalWidth / 2
      diff = modalLeft
      if (modalLeft < 5) modalLeft = 5
      if (modalLeft + modalWidth > windowWidth) modalLeft = windowWidth - modalWidth - 5
      if (modalPosition === 'top') {
        this.option.anglesite = 'bottom'
      }
      if (modalPosition === 'bottom') {
        this.option.anglesite = 'top'
      }
      diff = diff - modalLeft
      modalAngleLeft = (modalWidth / 2 - modalAngleSize + diff)
      modalAngleLeft = Math.max(Math.min(modalAngleLeft, modalWidth - modalAngleSize * 2 - 6), 6)
      this.option.ang_left = modalAngleLeft
      this.option.pop_top = modalTop
      this.option.pop_left = modalLeft
    }
  },
  attached: function () {
    this.sizePopover()
  },
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'PopoverOverlay'
    })
  },
  beforeDestroy: function () {
    this.$root.bodyChildren.delete(function (item) {
      return item.comp === 'PopoverOverlay'
    })
  }
}
</script>
<style>
.popover.popover-transition {
  display: block;
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) scale(1);
  transform: translate3d(0, 0, 0) scale(1);
  transition-property: opacity;
}

.popover.popover-enter {
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0) scale(1.185);
  transform: translate3d(0, 0, 0) scale(1.185);
}

.popover.popover-leave {
  opacity: 0;
}
</style>



