<template>
  <div class="popup-overlay" v-show="option.show" transition="popup-overlay">
  </div>
</template>
<script>
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        show: true
      }
    }
  },
  methods: {}
}
</script>
<style>
.popup-overlay.popup-overlay-transition {
  visibility: visible;
  opacity: 1;
}

.popup-overlay.popup-overlay-enter,
.popup-overlay.popup-overlay-leave {
  visibility: hidden;
  opacity: 0;
}
</style>



