var picker
export default {
  init: function (value) {
    if (!picker) {
      picker = value
    }
  },
  show: function (query) {
    window.vm.bodyChildren.push({
      comp: 'Picker',
      query: query
    })
  },
  close: function () {
    window.vm.bodyChildren.delete(function (item) {
      return item.comp === 'Picker'
    })
  }
}



