<template>
  <page :index="index">
    <navbar title="用户信息"></navbar>
    <page-content :data.sync="data">
      <div class="content-block-title">个性信息</div>
      <div class="list-block">
        <ul>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">头像</div>
                <div class="item-input">
                  <img :src="data.head" width="80" height="80" style="border-radius: 100%;margin: 8px 0">
                </div>
              </div>
            </div>
          </li>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">姓名</div>
                <div class="item-input">
                  <input type="text" placeholder="你的姓名" v-model="data.name" :readOnly="!isCurrentUser">
                </div>
              </div>
            </div>
          </li>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">性别</div>
                <div class="item-input">
                  <select v-model="data.sex" v-if="isCurrentUser">
                    <option value="male">男</option>
                    <option value="female">女</option>
                  </select>
                  <select v-model="data.sex" disabled v-if="!isCurrentUser">
                    <option value="male">男</option>
                    <option value="female">女</option>
                  </select>
                </div>
              </div>
            </div>
          </li>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">生日</div>
                <div class="item-input">
                  <input type="date" placeholder="出生日期" v-model="data.birthday" :readOnly="!isCurrentUser">
                </div>
              </div>
            </div>
          </li>
          <li class="align-top">
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">自述</div>
                <div class="item-input">
                  <textarea placeholder="什么都没有" v-model="data.desc" v-if="isCurrentUser"></textarea>
                  <textarea placeholder="什么都没有" v-model="data.desc" disabled v-if="!isCurrentUser"></textarea>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="content-block-title">所在地址</div>
      <div class="list-block">
        <ul>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">省</div>
                <div class="item-input">
                  <input type="text" placeholder="省份" value="广东省" :readOnly="!isCurrentUser">
                </div>
              </div>
            </div>
          </li>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">市</div>
                <div class="item-input">
                  <input type="text" placeholder="市级" value="广州市" :readOnly="!isCurrentUser">
                </div>
              </div>
            </div>
          </li>
          <li>
            <div class="item-content">
              <div class="item-inner">
                <div class="item-title label">地址</div>
                <div class="item-input">
                  <input type="text" placeholder="所在地址" value="白云索道" :readOnly="!isCurrentUser">
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="content-block" v-if="isCurrentUser">
        <a class="button button-big button-fill" v-tap="save">保存</a>
      </div>
    </page-content>
  </page>
</template>
<script>
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      data: undefined,
      isCurrentUser: false
    }
  },
  methods: {
    save: function () {
      window.vm.$v7.$indicator.show()
      var params = {
        name: this.data.name,
        sex: this.data.sex,
        birthday: this.data.birthday,
        desc: this.data.desc
      }
      this.$http.get(this.apiUrl + 'user/save', params).then(function (response) {
        window.vm.$v7.$indicator.hide()
        window.vm.notify('保存成功')
      }, function (response) {
        window.vm.$v7.$indicator.hide()
        window.vm.notify(response.data.message)
      })
    }
  },
  ready: function () {
    if (this.id === null || (this.$root.user && this.$root.user.id) === this.id) {
      this.isCurrentUser = true

      if (this.$root.user) {
        this.data = this.$root.user
      } else {
        this.isLoginUnWatch = this.$watch('$root.isLogin', function () {
          this.data = this.$root.user
          this.isLoginUnWatch()
          delete this.isLoginUnWatch
        })
      }
    } else {
      this.isCurrentUser = false

      this.$http.get(this.apiUrl + 'user/info', {
        id: this.id
      }).then(function (response) {
        this.data = response.data
      }, function (response) {})
    }
    console.log(this.data)
  }
}
</script>



