<template>
  <div class="photo">
    <canvas class="exif-canvas" v-el:canvas></canvas>
  </div>
  <div class="progressbar progress-bar" :data="progress" v-show="progress>= -100 && progress < 0">
    <span :style="'transform: translate3d(' +progress+ '%, 0px, 0px)';"></span>
  </div>
  <div class="delete-photo" v-tap="delete">
    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 48 48" fill="#999">
      <path d="M24 48C10.7 48 0 37.3 0 24S10.7 0 24 0s24 10.7 24 24-10.7 24-24 24zm0-42C14 6 6 14 6 24s8 18 18 18 18-8 18-18S34 6 24 6zm8.5 26.5c-1.2 1.2-3 1.2-4.3 0L24 28.2l-4.2 4.3c-1.2 1.2-3 1.2-4.3 0s-1.2-3 0-4.3l4.3-4.2-4.3-4.2c-1.2-1.2-1.2-3 0-4.3s3-1.2 4.3 0l4.2 4.3 4.2-4.3c1.2-1.2 3-1.2 4.3 0s1.2 3 0 4.3L28.2 24l4.3 4.2c1.2 1.2 1.2 3 0 4.3z" />
    </svg>
  </div>
</template>
<script>
import EXIF from 'exif-js'
var uploadQueue = []

var isUploading = false

var pushUpload = function (scope) {
  uploadQueue.push(scope)
  upload()
}

var upload = function () {
  if (isUploading) return
  var scope = uploadQueue.shift()
  if (!scope) return
  isUploading = true
  var dataurl = scope.$els.canvas.toDataURL('image/jpeg')
  var arr = dataurl.split(',')
  var mime = arr[0].match(/:(.*?);/)[1]
  var bstr = window.atob(arr[1])
  var n = bstr.length
  var u8arr = new Uint8Array(n)

  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  var blob = new window.Blob([u8arr], {
    type: mime
  })

  scope.$http.get(scope.apiUrl + 'upload/getToken').then(function (response) {
    scope.file.key = response.data.key
    var xhr = new window.XMLHttpRequest()
    xhr.open('POST', 'http://up.qiniu.com', true)
    var startDate = new Date().getTime()
    var formData = new window.FormData()
    formData.append('key', response.data.key)
    formData.append('token', response.data.token)
    formData.append('file', blob)
    var taking
    xhr.upload.addEventListener('progress', function (evt) {
      if (evt.lengthComputable) {
        var nowDate = new Date().getTime()
        taking = nowDate - startDate
        var x = (evt.loaded) / 1024
        var y = taking / 1000
        var uploadSpeed = (x / y)
        var formatSpeed
        if (uploadSpeed > 1024) {
          formatSpeed = (uploadSpeed / 1024).toFixed(2) + 'Mb/s'
        } else {
          formatSpeed = uploadSpeed.toFixed(2) + 'Kb/s'
        }
        scope.speed = formatSpeed
        scope.progress = Math.round(evt.loaded * 100 / evt.total - 100)
      }
    }, false)

    xhr.onreadystatechange = function () {
      scope.status = xhr.status
      if (xhr.readyState === 4 && xhr.status === 200 && xhr.responseText !== '') {
        isUploading = false
        upload()
        scope.$dispatch('upload', 'success', scope.index)
      } else if (xhr.status !== 200 && xhr.responseText) {
        isUploading = false
        window.vm.$v7.$modal.show({
          title: '提示',
          text: '上传图片出错!',
          buttons: [{
            text: '确定',
            onClick: function () {
              scope.$dispatch('upload', 'failure', scope.index)
              setTimeout(() => scope.$dispatch('delete', scope.index), 100)
              upload()
            }
          }]
        })
      }
    }
    xhr.send(formData)
  }, function (response) {
    isUploading = false
    window.vm.$v7.$modal.show({
      title: '提示',
      text: '获取token出错!',
      buttons: [{
        text: '确定',
        onClick: function () {
          scope.$dispatch('upload', 'failure', scope.index)
          setTimeout(() => scope.$dispatch('delete', scope.index), 100)
          upload()
        }
      }]
    })
  })
}

export default {
  props: ['file', 'index'],
  data: function () {
    return {
      progress: -100
    }
  },
  methods: {
    delete: function () {
      setTimeout(() => this.$dispatch('delete', this.index), 100)
    }
  },
  ready: function () {
    EXIF.getData(this.file, () => {
      EXIF.getAllTags(this.file)
      var mpImg = new window.MegaPixImage(this.file)
      mpImg.render(this.$els.canvas, {
        maxWidth: 2000,
        maxHeight: 2000,
        orientation: EXIF.getTag(this, 'Orientation')
      }, (response) => {
        pushUpload(this)
      })
    })
  }
}
</script>
<style>
.publish .gallery {
  width: 100%;
  margin: -4px 0;
  padding: 0 4px;
}

.publish .gallery .photo-item {
  width: calc(100%/3 - 8px);
  float: left;
  margin: 4px;
  position: relative;
}

.publish .gallery .photo-item .photo {
  padding: 50%;
  background-position: center;
  background-size: cover;
}

.publish .gallery .photo-item .photo .camera {
  background-color: #eee;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.publish .gallery .photo-item .progress-bar {
  height: 8px;
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.publish .gallery .photo-item .photo input[type="file"] {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  background-position: center;
  background-size: cover;
}

.publish .gallery .photo-item .exif-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.publish .gallery .photo-item .delete-photo {
  width: 16px;
  height: 16px;
  position: absolute;
  top: 5px;
  right: 13px;
}

.publish .list-block .item-link {
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
  display: block;
  color: inherit;
  position: relative;
  overflow: hidden;
  z-index: 0;
}
</style>



