<template>
  <div class="page-content" @touchstart="touchstart" @touchmove="touchmove">
    <slot></slot>
    <div class="load-layer" v-if="!data">
      <preloader v-show="!message"></preloader>
      <div class="content-block-title" v-show="message" v-text="message"></div>
      <a v-show="message" class="button button-big button-fill" v-tap:='reload()'>重新加载</a>
    </div>
  </div>
</template>
<script>
export default {
  props: ['data'],
  data: function () {
    return {}
  },
  methods: {
    touchstart: function (e) {
      this.pageY = e.targetTouches[0].pageY
    },
    touchmove: function (e) {
      var diffY = e.targetTouches[0].pageY - this.pageY
      if (diffY === 0) {

      } else if (diffY > 0 && diffY > this.startScrollTop && this.$el.scrollTop === 0) {
        e.preventDefault()
      } else if (diffY < 0 && this.$el.scrollHeight - this.$el.scrollTop <= this.$el.offsetHeight) {
        e.preventDefault()
      }
    }
  },
  ready: function () {}
}
</script>
<style>
</style>



