'use strict'

import Color from './components/Color'
import View from './components/View'
import Page from './components/Page'
import PageContent from './components/PageContent'
import PageList from './components/PageList'
import Navbar from './components/Navbar'

import Modal from './components/Modal'
import ModalOverlay from './components/ModalOverlay'
import Preloader from './components/Preloader'

import Panel from './components/Panel'
import PanelOverlay from './components/PanelOverlay'

import Indicator from './components/Indicator'
import IndicatorOverlay from './components/IndicatorOverlay'

import Popover from './components/Popover'
import PopoverOverlay from './components/PopoverOverlay'

import Notifications from './components/Notifications'

import Picker from './components/Picker'
import PickerOverlay from './components/PickerOverlay'

import Popup from './components/Popup'
import PopupOverlay from './components/PopupOverlay'

import Morph from './components/Morph'
import MorphOverlay from './components/MorphOverlay'

import tap from './directives/tap'
import link from './directives/link'
import list from './directives/list'

import $indicator from './operates/$indicator'
import $modal from './operates/$modal'
import $notifications from './operates/$notifications'
import $panel from './operates/$panel'
import $picker from './operates/$picker'
import $popover from './operates/$popover'
import $popup from './operates/$popup'
import $comment from './operates/$comment'
import $preloader from './operates/$preloader'
import $router from './operates/$router'
import $view from './operates/$view'

import array from './array'
array

export default {
  install: function (Vue, options) {
    var htmlClass = []
    htmlClass.push('pixel-ratio-' + (window.devicePixelRatio || 1))
    if (window.devicePixelRatio >= 2) htmlClass.push('retina')
    if (Vue.util.isIos) {
      htmlClass.push('ios')
      let systemVersion = window.navigator.userAgent.match('OS\\s(\\d+_\\d+)')[1].split('_')
      htmlClass.push('ios-' + systemVersion[0])
      htmlClass.push('ios-' + systemVersion.join('-'))
    }
    if (Vue.util.isAndroid) {
      htmlClass.push('android')
      let systemVersion = window.navigator.userAgent.match('Android\\s(\\d+.\\d+)')[1].split('.')
      htmlClass.push('android-' + systemVersion[0])
      htmlClass.push('android-' + systemVersion.join('-'))
    }
    document.documentElement.classList.add.apply(document.documentElement.classList, htmlClass)

    Vue.component('Color', Color)
    Vue.component('View', View)
    Vue.component('Page', Page)
    Vue.component('PageContent', PageContent)
    Vue.component('PageList', PageList)
    Vue.component('Navbar', Navbar)

    Vue.component('Modal', Modal)
    Vue.component('ModalOverlay', ModalOverlay)
    Vue.component('Preloader', Preloader)

    Vue.component('Panel', Panel)
    Vue.component('PanelOverlay', PanelOverlay)

    Vue.component('Indicator', Indicator)
    Vue.component('IndicatorOverlay', IndicatorOverlay)

    Vue.component('Popover', Popover)
    Vue.component('PopoverOverlay', PopoverOverlay)

    Vue.component('Notifications', Notifications)

    Vue.component('Picker', Picker)
    Vue.component('PickerOverlay', PickerOverlay)

    Vue.component('Popup', Popup)
    Vue.component('PopupOverlay', PopupOverlay)

    Vue.component('Morph', Morph)
    Vue.component('MorphOverlay', MorphOverlay)

    Vue.directive('tap', tap)
    Vue.directive('link', link)
    Vue.directive('list', list)

    Vue.prototype.$v7 = {
      $indicator: $indicator,
      $modal: $modal,
      $notify: $notifications,
      $panel: $panel,
      $picker: $picker,
      $popover: $popover,
      $popup: $popup,
      $comment: $comment,
      $preloader: $preloader,
      $router: $router,
      $view: $view
    }
  }
}



