<template>
  <div class="views">
    <div class="view view-main">
      <div class="pages">
        <component v-for="page in pages" :is="page.comp" :index="$index" :id="page.id" :query="page.query"></component>
      </div>
    </div>
  </div>
</template>
<script>
import $view from './../operates/$view'
import Hammer from 'hammerjs'

import Vue from 'vue'

const transformProp = (document.body.style.transform === undefined && document.body.style.webkitTransform !== undefined) ? 'webkitTransform' : 'transform'
const swipebackPageShadow = document.createElement('div')
swipebackPageShadow.className = 'swipeback-page-shadow'

Vue.transition('page', {
  enter: function (el) {
    if (el.previousElementSibling) {
      el.appendChild(swipebackPageShadow)
    }

    // 加载第一个页面不需要加阴影
  },
  afterEnter: function (el) {
    if (swipebackPageShadow.parentNode === el) {
      swipebackPageShadow.remove()
    }
  },
  leave: function (el) {
    el.appendChild(swipebackPageShadow)
  },
  beforeLeave: function (el) {
    swipebackPageShadow.style.opacity = ''
    el.style[transformProp] = ''
    el.style.transitionDuration = ''

    el.previousElementSibling.style.opacity = ''
    el.previousElementSibling.style[transformProp] = ''
    el.previousElementSibling.style.transitionDuration = ''
  },
  afterLeave: function (el) {
    if (swipebackPageShadow.parentNode === el) {
      swipebackPageShadow.remove()
    }
  }
})

export default {
  props: ['index', 'query'],
  data: function () {
    return {
      pages: [],
      hammer: undefined
    }
  },
  watch: {
    'pages.length': function () {
      this.$broadcast('router:change', this.pages.length)
    }
  },
  methods: {
    load: function (comp, id, query) {
      this.pages.push({
        comp: comp,
        id: id,
        query: query
      })
    },
    back: function () {
      if (this.pages.length !== 1) {
        this.pages.pop()
      }
      // 使用vue.nextTick无法触动动画，所以需要使用setTimeout
    },
    clear: function () {
      this.pages.splice(0, this.pages.length)
    }
  },
  ready: function () {
    this.hammer = new Hammer(this.$el)

    if (true || !this.$util.inBrowser || (this.$util.isWechat && this.$util.isIos)) {
      var pageCenter
      var pageLeft
      var viewWidth
      var startDelta

      this.hammer.on('panstart', function (event) {
        startDelta = event.deltaX
        if (event.changedPointers[0].pageX < (window.vm.$util.isWechat ? 300 : 30)) {
          viewWidth = event.srcEvent.currentTarget.offsetWidth
          pageCenter = event.srcEvent.currentTarget.querySelector('.page.page-out + .page')
          if (pageCenter) {
            window.vm.tapAction = false
            pageLeft = event.srcEvent.currentTarget.querySelector('.page.page-out')

            pageCenter.appendChild(swipebackPageShadow)
            pageCenter.style.transitionDuration = '0ms'

            pageLeft.style.transitionDuration = '0ms'
          }
        }
      })
      this.hammer.on('panend', function (event) {
        if (pageCenter) {
          window.vm.tapAction = true

          var parsent = event.deltaX / viewWidth
          if (parsent > 0.5) {
            window.history.back()
          } else {
            swipebackPageShadow.style.opacity = ''
            pageCenter.style[transformProp] = ''
            pageCenter.style.transitionDuration = ''

            pageLeft.style.opacity = ''
            pageLeft.style[transformProp] = ''
            pageLeft.style.transitionDuration = ''
          }
          // 滑动大于50%时，无论swiper如何都返回

          pageCenter = null
          pageLeft = null
          viewWidth = null
        }
      })
      this.hammer.on('pan', function (event) {
        if (pageCenter && event.deltaX > 0) {
          var delatX = event.deltaX > viewWidth ? viewWidth : event.deltaX
          delatX = Math.max(delatX - startDelta, 0)
          pageCenter.style[transformProp] = 'translate3d(' + delatX + 'px, 0, 0)'

          var parsent = (delatX / viewWidth).toFixed(3)
          swipebackPageShadow.style.opacity = 1 - parsent
          pageLeft.style.opacity = 1 - (1 - parsent) * 0.1
          pageLeft.style[transformProp] = 'translate3d(' + (20 * parsent - 20) + '%, 0, 0)'
        }
      })
      this.hammer.on('swiperight', function (event) {
        var parsent = event.deltaX / viewWidth
        if (parsent <= 0.5) {
          window.history.back()
        }
        // 滑动大于50%时，由panend事件返回
      })
    }
    $view.init(this)
  },
  beforeDestroy: function () {
    this.hammer.destroy()
    this.hammer = undefined
  }
}
</script>
<style>
</style>



