import $router from './../operates/$router'

export default {
  bind: function () {
    this.el.onTap = this
  },
  unbind: function () {
    delete this.el.onTap
  },
  update: function (link) {
    if (this.arg) {
      this.margin = parseInt(this.arg)
    }
    if (this.modifiers.bubbles) this.bubbles = true
    if (this.modifiers.clear) this.clear = true
    this.link = link
  },
  handler: function () {
    if (this.link === 'back') {
      $router.back()
    } else {
      $router.go(this.link.comp, this.link.id, this.link.query)
    }
  }
}

// 整个事件由tap控制，由于和tap的acceptStatement不兼容，所以使用添加了一个指令用于控制view



