<template>
  <div class="page-content pull-to-refresh-no-navbar pull-to-refresh-content" @scroll="scroll" @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend" :class="{'transitioning':!isPull,'pull-down':isPull && !isRefresh,'pull-up':isRefresh,'refreshing':isRefreshing,'getting':isGetting}">
    <pull-refresh v-ref:pull-refresh v-show="data"></pull-refresh>
    <slot></slot>
    <div class="load-layer" v-if="!data">
      <preloader v-show="!message"></preloader>
      <div class="content-block-title" v-show="message" v-text="message"></div>
      <a v-show="message" class="button button-big button-fill bg-orange" v-tap:='reload()'>重新加载</a>
    </div>
    <infinite-scroll v-ref:infinite-scroll v-show="data"></infinite-scroll>
  </div>
</template>
<script>
const transformProp = (document.body.style.transform === undefined && document.body.style.webkitTransform !== undefined) ? 'webkitTransform' : 'transform'
export default {
  props: ['url', 'property', 'postData', 'data', 'list'],
  data: function () {
    return {
      message: null,
      isPull: false,
      // 是否响应下拉，用于防止正在loading的时候被下拉
      isRefresh: false,
      // 是否达到refresh，放手时会触发刷新
      isPulling: false,
      // pagecontent是否已经下拉
      isRefreshing: false,
      // 是否正在处于刷新状态
      startDelta: 0,
      // 用于防止下拉时10px的跳动
      startScrollTop: 0,
      // 用于防止scrollTop不为0时下拉跳动
      isGetting: false
        // 无限滚动
    }
  },
  watch: {
    isPulling: function (newValue) {
      window.vm.tapAction = !newValue
    }
  },
  methods: {
    load: function () {
      this.$http.get(this.apiUrl + this.url, this.postData).then((response) => {
        this.list = response.data[this.property]
        delete response.data[this.property]
        this.data = response.data
      }, (response) => {
        this.message = response.data.message
      })
    },
    reload: function () {
      this.load()
    },
    touchstart: function (e) {
      this.pageY = e.targetTouches[0].pageY
      this.pullHeight = this.$refs.pullRefresh.$el.offsetHeight
      if (!this.isRefreshing) {
        this.startScrollTop = this.$el.scrollTop
          // 用于防止scrollTop不为0时下拉跳动
        this.isPull = true
      } else {
        this.isPull = false
      }
    },
    touchmove: function (e) {
      var diffY = e.targetTouches[0].pageY - this.pageY
      if (diffY === 0) {

      } else if (diffY > 0 && diffY > this.startScrollTop && this.$el.scrollTop === 0) {
        if (this.isPull) {
          var delta = Math.pow(diffY - this.startScrollTop, 0.85)
          this.$el.style[transformProp] = 'translate3D(0,' + delta + 'px,0)'

          if (!this.isPulling) this.isPulling = true

          if (delta > this.pullHeight) {
            this.isRefresh = true
          } else {
            this.isRefresh = false
          }
        }

        // 下拉逻辑
        e.preventDefault()
      } else if (diffY < 0 && this.$el.scrollHeight - this.$el.scrollTop <= this.$el.offsetHeight) {
        e.preventDefault()
      } else if (this.isPulling) {
        this.isPulling = false
        this.$el.style[transformProp] = ''
          // 防止diffy<0且scrollTop>0时下拉无法复位的问题
      }
    },
    touchend: function (e) {
      if (this.isPull) {
        if (this.isRefresh) {
          this.isRefresh = false
          this.refreshing()
        }
        this.$el.style[transformProp] = ''
        this.isPull = false
        this.isPulling = false
      }
    },
    refreshing: function () {
      this.isRefreshing = true
      this.$http.get(this.apiUrl + this.url, this.postData ? this.$util.extend({
        index: this.data.prev
      }, this.postData) : {
        index: this.data.prev
      }).then((response) => {
        this.list.unshift.apply(this.list, response.data[this.property])
        delete response.data[this.property]
        response.data.prev = response.data.prev || this.data.prev
        response.data.next = this.data.next
        this.data = response.data
        this.isRefreshing = false
      }, function (response) {
        window.vm.$v7.$notify.show(response.data.message)
        this.isRefreshing = false
      })
    },
    scroll: function () {
      if (this.isGetting === false && this.data.next && this.$el.scrollHeight - (this.$el.scrollTop + this.$el.offsetHeight * 2) <= 0) {
        this.getting()
      }
      if (this.isRefreshing === false && this.$el.scrollTop < 0) {
        if (this.$el.scrollTop < -this.pullHeight) {
          this.isRefresh = true
        } else {
          this.isRefresh = false
        }
      }
      // 兼容ios的scrollTop为负
    },
    getting: function () {
      this.isGetting = true
      this.$http.get(this.apiUrl + this.url, this.postData ? this.$util.extend({
        index: this.data.next
      }, this.postData) : {
        index: this.data.next
      }).then((response) => {
        this.list.push.apply(this.list, response.data[this.property])
        delete response.data[this.property]
        response.data.prev = this.data.prev
        this.data = response.data
        this.isGetting = false
      }, function (response) {
        window.vm.$v7.$notify.show(response.data.message)
        this.isGetting = false
      })
    }
  },
  ready: function () {
    this.load()
  }
}
</script>
<style>
.load-layer {
  position: absolute;
  left: 10%;
  top: 40%;
  z-index: 13500;
  width: 80%;
}

.load-layer .preloader {
  display: block;
  margin: 0 auto;
}

.ios .load-layer .preloader {
  width: 34px;
  height: 34px;
}

.android .load-layer .preloader {
  width: 32px;
  height: 32px;
}

.load-layer .content-block-title {
  text-align: center;
}

.load-layer .button {
  margin: 10px 25%;
}
</style>



