<template>
  <!-- 由于暂时无用，所以未审核 -->
  <div>picker</div>
</template>
<script>
import $picker from './../operates/$picker'
export default {
  props: ['index', 'query'],
  data: function () {
    return {

    }
  },
  methods: {
    close: function () {

    }
  },
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'PickerOverlay'
    })
  },
  beforeDestroy: function () {
    this.$root.bodyChildren.delete(function (item) {
      return item.comp === 'PickerOverlay'
    })
  },
  ready: function () {
    $picker.init(this)
  }
}
</script>
