window.Array.prototype.exist = function (func) {
  return this.first(func) != null
}
window.Array.prototype.first = function (func) {
  return this[this.firstIndex(func)]
}
window.Array.prototype.toObject = function (func, value) {
  var obj = {}
  var t = arguments.length === 2 ? typeof value : null
  for (var i = 0; i < this.length; i++) {
    obj[func(this[i], i)] = t ? (t === 'function' ? value(this[i], i) : value) : this[i]
  }
  return obj
}
window.Array.prototype.toArray = function () {
  return this
}
window.Array.prototype.where = function (func) {
  var arr = []
  for (var i = 0; i < this.length; i++) {
    if (func(this[i], i)) arr.push(this[i])
  }
  return arr
}
window.Array.prototype.take = function (count) {
  var arr = []
  if (count > this.length) count = this.length
  for (var i = 0; i < count; i++) {
    arr.push(this[i])
  }
  return arr
}
window.Array.prototype.select = function (func) {
  var arr = []
  for (var i = 0; i < this.length; i++) {
    arr.push(func(this[i], i))
  }
  return arr
}
window.Array.prototype.each = function (func) {
  for (var i = 0; i < this.length; i++) {
    func(this[i], i)
  }
  return this
}
window.Array.prototype.union = function (second, func) {
  // 获取并集
  // 数据对比将会默认使用toString进行对比
  // 如果数据类型为object,array请务必传入比较器,否则将返回空的数组
  // 无法区分number中的1和string中的'1'
  var obj = second.toObject(func || function (item) {
    return item
  })
  var arr = []
  for (var i = 0; i < this.length; i++) {
    if (obj.hasOwnProperty(func ? func(this[i], i) : this[i])) arr.push(this[i])
  }
  return arr
}
window.Array.prototype.except = function (second, func) {
  // 获取差集
  // this有second没有的
  // 数据对比将会默认使用toString进行对比
  // 如果数据类型为object,array请务必传入比较器,否则将返回空的数组
  // 无法区分number中的1和string中的'1'
  var obj = second.toObject(func || function (item) {
    return item
  })
  var arr = []
  for (var i = 0; i < this.length; i++) {
    if (obj.hasOwnProperty(func ? func(this[i], i) : this[i])) continue
    arr.push(this[i])
  }
  return arr
}
window.Array.prototype.distinct = function (func) {
  // 去除重复
  // 数据对比将会默认使用toString进行对比
  // 如果数据类型为object,array请务必传入比较器,否则将返回空的数组
  // 无法区分number中的1和string中的'1'
  var obj = {}
  var arr = []
  for (var i = 0; i < this.length; i++) {
    var k = func ? func(this[i], i) : this[i]
    if (obj.hasOwnProperty(k)) continue
    obj[k] = true
    arr.push(this[i])
  }
  return arr
}
window.Array.prototype.firstIndex = function (func) {
  for (var i = 0; i < this.length; i++) {
    if (func(this[i], i)) return i
  }
  return -1
}
window.Array.prototype.lastIndex = function (func) {
  for (var i = this.length - 1; i > -1; i--) {
    if (func(this[i], i)) return i
  }
  return -1
}
window.Array.prototype.delete = function (func) {
  var index = this.lastIndex(func)
  if (index !== -1) {
    return this.splice(index, 1)[0]
  } else {
    return undefined
  }
}
window.Array.prototype.find = function (func) {
  return this[this.firstIndex(func)]
}
export default window.Array



