import $modal from './$modal'
import Vue from 'vue'
Vue.component('PreloaderModal', {
  template: '<div class="modal-text"><preloader></preloader></div>',
  ready: function () {
    this.$el.parentNode.parentNode.classList.add('modal-preloader')
  }
})
export default {
  show: function (title) {
    title = title || 'V7'
    $modal.show({
      title: title,
      afterComponent: 'PreloaderModal'
    })
  },
  hide: function () {
    $modal.hide()
  }
}



