<template>
  <div class="panel panel-left panel-cover" v-show="isShow" transition="panel">
    <div class="page">
      <page-content>
        <slot></slot>
      </page-content>
      <div class="swipeback-page-shadow"></div>
    </div>
  </div>
</template>
<script>
import $panel from './../operates/$panel'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      isShow: false
    }
  },
  watch: {
    'isShow': function (newValue, oldValue) {
      if (newValue) {
        window.vm.bodyChildren.push({
          comp: 'PanelOverlay'
        })
      } else {
        window.vm.bodyChildren.delete(function (item) {
          return item.comp === 'PanelOverlay'
        })
      }
    }
  },
  methods: {
    open: function () {
      this.isShow = true
    },
    close: function () {
      this.isShow = false
    },
    toggle: function () {
      this.isShow = !this.isShow
    }
  },
  ready: function () {
    $panel.init(this)
  }
}
</script>
<style>
.panel.panel-transition {
  display: block;
  -webkit-transform: translate3d(260px, 0, 0);
  transform: translate3d(260px, 0, 0);
  overflow: visible;
}

.panel.panel-enter,
.panel.panel-leave {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.panel.panel-transition .swipeback-page-shadow {
  right: initial;
  left: 100%;
  transform: scaleX(-1);
}
</style>



