<template>
  <div class="notifications list-block" :class="{'media-list':$util.isIos}">
    <ul>
      <li v-for="item in notify" class="notification-item" transition="notification-item" v-tap="click($index)">
        <div class="item-content">
          <div class="item-media" v-html="item.media" v-if="item.media">
          </div>
          <div class="item-inner" v-if="!$util.isAndroid">
            <div class="item-title-row">
              <div class="item-title" v-text="item.title" v-if="item.title"></div>
              <div class="item-after">
                <a class="close-notification" v-tap="close($index)">
                  <span></span>
                </a>
              </div>
            </div>
            <div class="item-subtitle" v-text="item.subtitle" v-if="item.subtitle"></div>
            <div class="item-text" v-text="item.message" v-if="item.message"></div>
          </div>
          <div class="item-inner" v-if="$util.isAndroid">
            <!-- <div class="item-title" v-text="item.title" v-if="item.title"></div> -->
            <div class="item-title" v-text="item.message" v-if="item.message"></div>
            <div class="item-after">
              <a class="button  close-notification" v-tap="close($index)" v-text="item.closeText || '关闭'"></a>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import $notifications from './../operates/$notifications'
import Vue from 'vue'
Vue.transition('notification-item', {
  enter: function (el) {
    var height = el.offsetHeight
    el.style.height = 0 + 'px'
    setTimeout(
      function () {
        el.style.opacity = '1'
        el.style.height = height + 'px'
      })
  },
  leave: function (el) {
    setTimeout(
      function () {
        el.style.opacity = '0'
        el.style.height = 0 + 'px'
      })
  }
})

export default {
  props: ['index', 'query'],
  data: function () {
    return {
      isMaterial: false,
      notify: []
    }
  },
  methods: {
    click: function (index) {
      var item = this.notify[index]
      if (item.onClick) {
        item.onClick()
      }
      if (item.closeOnClick) {
        this.close(index)
      }
    },
    close: function (index) {
      var item = this.notify.splice(index, 1)
      if (item[0].onClose) item[0].onClose()
    }
  },
  ready: function () {
    $notifications.init(this)
  }
}
</script>
<style>
.notifications {
  display: block;
}

.notification-item-transition {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
</style>



