<template>
  <page :index="index">
    <navbar :index="index" title="气泡详情"></navbar>
    <page-list v-ref:pagelist url="status/show" property="comment" :data.sync="data" :list.sync="list" :post-data="{id:id}" @touchstart="touchstart" @touchend="touchend">
      <status-card :item="data"></status-card>
      <div class="content-block-title" style="margin-top: 20px">评论列表</div>
      <div class="list-block media-list inset" style="margin: 0px 10px 5px 10px;">
        <ul>
          <li class="item-content" v-for="part in list">
            <div class="item-media">
              <img :src="part.user.head" width="34" height="34" v-tap="showUserHome(part.user.id)">
            </div>
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title" v-text="part.user.name"></div>
                <div class="item-after" style="font-size: 12px; margin-top: 2px" v-dateformat="part.create_at"></div>
              </div>
              <div class="item-text" style="height: auto; overflow: visible; -webkit-line-clamp: inherit;" v-text="part.content" v-tap="toComment"></div>
            </div>
          </li>
        </ul>
      </div>
      <comment v-ref:comment :item="data" :isscrollshow="true"></comment>
    </page-list>
  </page>
</template>
<script>
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      data: null,
      list: [],
      startScrollHeight: 0
        // 滚动开始前的已滚动高度,用来判断是向上滚动还是向下滚动
    }
  },
  methods: {
    showUserHome: function (id) {
      this.$v7.$router.go('UserHome', id)
    },
    toComment: function () {
      window.vm.$v7.$comment.comment({
        hideComment: false,
        commentId: this.id
      })
    },
    touchstart: function (e) {
      this.startScrollHeight = this.$refs.pagelist.$el.scrollTop
    },
    touchend: function (e) {
      if (this.$refs.pagelist.$el.scrollTop > this.startScrollHeight) {
        window.vm.$v7.$comment.toggleScrollToolbars({
          direction: 'down'
        })
      } else {
        window.vm.$v7.$comment.toggleScrollToolbars({
          direction: 'up'
        })
      }
    }
  },
  ready: function () {}
}
</script>
<style>
.bubble .list-block.media-list .item-title-row {
  min-width: 34px;
}
</style>



