<template>
  <div class="modal-overlay" v-show="option.show" @click="closePopover()" transition="modal-overlay">
  </div>
</template>
<script>
import $popover from './../operates/$popover'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        show: true
      }
    }
  },
  methods: {
    closePopover: function () {
      $popover.hide()
    }
  }
}
</script>
<style>
.modal-overlay.modal-overlay-transition {
  visibility: visible;
  opacity: 1;
}

.modal-overlay.modal-overlay-enter,
.modal-overlay.modal-overlay-leave {
  visibility: hidden;
  opacity: 0;
}
</style>



