<template>
  <!-- 由于暂时无用，所以未审核 -->
  <div class="modal-overlay" v-show="option.show" @click="closePicker()" transition="modal-overlay">
  </div>
</template>
<script>
import $picker from './../operates/$picker'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        show: true
      }
    }
  },
  methods: {
    closePicker: function () {
      $picker.close()
    }
  }
}
</script>
<style>
.modal-overlay.modal-overlay-transition {
  visibility: visible;
  opacity: 1;
}

.modal-overlay.modal-overlay-enter,
.modal-overlay.modal-overlay-leave {
  visibility: hidden;
  opacity: 0;
}
</style>



