export default {
  show: function (query) {
    window.vm.bodyChildren.push({
      comp: 'Modal',
      query: query
    })
  },
  hide: function () {
    window.vm.bodyChildren.delete(function (item) {
      return item.comp === 'Modal'
    })
  }
}



