<template>
  <!-- 未审核 -->
  <div class="modal-overlay" v-show="option.show" @click="closeMorph()" transition="modal-overlay">
  </div>
</template>
<script>
import $morph from './../operates/$morph'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        show: true
      }
    }
  },
  methods: {
    closeMorph: function () {
      $morph.hide()
    }
  }
}
</script>
<style>
.modal-overlay.modal-overlay-transition {
  visibility: visible;
  opacity: 1;
}

.modal-overlay.modal-overlay-enter,
.modal-overlay.modal-overlay-leave {
  visibility: hidden;
  opacity: 0;
}
</style>



