<template>
  <page>
    <navbar title="气泡列表"></navbar>
    <page-list url="status/public" property="status" :data.sync="data" :list.sync="list" @scroll="scroll">
      <status-card v-for="part in list" v-tap="showBubble(part.id)" :item="part"></status-card>
    </page-list>
    <comment v-ref:comment :status.sync="currentStatus"></comment>
  </page>
</template>
<script>
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      data: null,
      list: [],
      currentStatus: false
    }
  },
  methods: {
    showBubble: function (id) {
      this.$v7.$router.go('Bubble', id)
    },
    scroll: function () {
      window.vm.$v7.$comment.toggleScrollToolbars({
        direction: 'scroll'
      })
    }
  },
  ready: function () {
    window.vm.$v7.$comment.hide()
  }
}
</script>
<style>
</style>



