<template>
  <div class="preloader-indicator-modal">
    <preloader class="preloader-white"></preloader>
  </div>
</template>
<script>
export default {
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'IndicatorOverlay'
    })
  },
  beforeDestroy: function () {
    window.vm.bodyChildren.delete(function (item) {
      return item.comp === 'IndicatorOverlay'
    })
  }
}
</script>



