<template>
  <div class="navbar">
    <div class="navbar-inner">
      <div class="left">
        <a class="back link icon-only" v-tap="openPanel" v-if="index==0">
          <i class="icon icon-bars" v-text='index'></i>
        </a>
        <a class="back link" v-link:16="'back'" v-if="index!=0">
          <i class="icon icon-back" v-text='index'></i>
          <span v-if="$util.isIos">返回</span>
        </a>
      </div>
      <div class="center" v-text="title"></div>
      <div class="right">
        <slot></slot>
      </div>
    </div>
  </div>
</template>
<script>
import $panel from './../operates/$panel'
export default {
  props: ['index', 'title'],
  data: function () {
    return {}
  },
  methods: {
    openPanel: function () {
      $panel.open()
    }
  },
  ready: function () {
    if (this.$util.isIos) {
      this.$el.querySelector('.left').style.width = this.$el.querySelector('.right').style.width =
        Math.max(this.$el.querySelector('.left').offsetWidth, this.$el.querySelector('.right').offsetWidth) + 'px'
    }
    if (this.$util.inBrowser) {
      this.$el.style.display = 'none'
      this.historyTitle = document.title
      document.title = this.title

      if (this.$util.isWechat && this.$util.isIos) {
        // ios下的微信需要使用iframe来修改标题
        var iframe = document.createElement('iframe')
        iframe.setAttribute('src', 'loading.png')
        setTimeout(function () {
          document.body.removeChild(iframe)
        }, 1000)
        document.body.appendChild(iframe)
      }
    } else {
      this.$parent.$el.classList.add('navbar-fixed')
    }
    // 防止标题左右偏移
  },
  beforeDestroy: function () {
    document.title = this.historyTitle

    if (this.$util.isWechat && this.$util.isIos) {
      var iframe = document.createElement('iframe')
      iframe.setAttribute('src', 'loading.png')
      setTimeout(function () {
        document.body.removeChild(iframe)
      }, 1000)
      document.body.appendChild(iframe)
    }
  }
}
</script>
<style>
</style>



