<template>
  <div class="pswp" tabindex="-1" role="dialog" aria-hidden="true" :index="index" :query="query">
    <div class="pswp__bg"></div>
    <div class="pswp__scroll-wrap">
      <div class="pswp__container">
        <div class="pswp__item"></div>
        <div class="pswp__item"></div>
        <div class="pswp__item"></div>
      </div>
      <div class="pswp__ui pswp__ui--hidden">
        <div class="pswp__counter">
        </div>
        <div class="pswp__preloader">
          <div class="pswp__preloader__icn">
            <div class="pswp__preloader__cut">
              <div class="pswp__preloader__donut"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['index', 'query'],
  data: function () {
    return {}
  },
  methods: {},
  compiled: function () {
    this.onpopstate = window.onpopstate
    window.history.pushState({}, ',', document.location.pathname)
    window.onpopstate = (event) => {
      this.gallery.close()
    }
  },
  ready: function () {
    var options = {
      history: false,
      closeOnVerticalDrag: false,
      tapToClose: true,
      captionEl: false
    }
    this.gallery = new window.PhotoSwipe(this.$el, window.PhotoSwipeUI_Default, this.query.map((item) => {
      return {
        w: 1000,
        h: 1000,
        src: this.staticUrl + item,
        loadComplete: function (item) {
          item.w = item.img.naturalWidth
          item.h = item.img.naturalHeight
        }
      }
    }), options)

    this.gallery.listen('destroy', () => {
      window.history.back()
    })

    this.gallery.init()

    this.gallery.listen('initialZoomInEnd', function (index, item) {
      if (this.currItem.w !== this.currItem.container.lastChild.naturalWidth || this.currItem.h !== this.currItem.container.lastChild.naturalHeight) {
        this.currItem.w = this.currItem.container.lastChild.naturalWidth
        this.currItem.h = this.currItem.container.lastChild.naturalHeight
        this.updateSize(true)
      }
    })

    // initialZoomInEnd事件必须要在init后面才能修复大小显示bug
  },
  beforeDestroy: function () {
    window.onpopstate = this.onpopstate
    delete this.onpopstate

    // 清空于路由相关的内容
  }
}
</script>
<style>
.pswp {
  z-index: 5000 !important
}

.pswp__img--placeholder--blank {
  background: initial !important
}

.pswp__counter {
  text-shadow: 1px 1px 3px #000;
}
</style>



