var count = 0
export default {
  show: function (query) {
    if (count === 0) {
      window.vm.bodyChildren.push({
        comp: 'Indicator',
        query: query
      })
    }
    count++
  },
  hide: function () {
    count--
    if (count === 0) {
      window.vm.bodyChildren.delete(function (item) {
        return item.comp === 'Indicator'
      })
    }
  }
}



