var notifications
var initParams
var isMaterial = document.documentElement.classList[0] === 'material'
export default {
  init: function (el) {
    if (!notifications) {
      notifications = el
      if (isMaterial && !initParams.closeText) initParams.closeText = 'close'
      notifications.isMaterial = isMaterial
      notifications.notify.unshift(initParams)
      var hold = initParams.hold || 5000
      setTimeout(function () {
        if (notifications.notify.length > 0) notifications.notify.splice(notifications.notify.length - 1, 1)
      }, hold)
    }
  },
  add: function (params) {
    if (notifications) {
      if (isMaterial && !params.closeText) params.closeText = 'close'
      isMaterial ? notifications.notify.push(params) : notifications.notify.unshift(params)
      var hold = params.hold || 5000
      setTimeout(function () {
        if (notifications.notify.length > 0) notifications.notify.splice(notifications.notify.length - 1, 1)
      }, hold)
    } else {
      window.vm.bodyChildren.push({
        comp: 'Notifications'
      })
      initParams = params
    }
  }
}



