import Vue from 'vue'

var target
var offsetTop
var offsetLeft
var offsetBottom
var offsetRight
Vue.nextTick(function () {
  window.vm.$watch('tapAction', function () {
    if (target) {
      target.classList.remove('active-state')
      target = null
    }
  })
})
document.documentElement.classList.add('watch-active-state')
  // 防止使用framework7自带的:active伪选择器
document.addEventListener('touchstart', function (event) {
  if (!window.vm.tapAction) return

  target = getTarget(event.target)
  if (target) {
    setTimeout(() => {
      if (target) target.classList.add('active-state')
    }, Vue.util.isAndroid ? 0 : 100)

    // 延迟是为了避免，如果用户需要滚动时，active-state的情况
    calcOffset()
    var pageContent = getPageContent(target)
    if (pageContent) pageContent.addEventListener('scroll', pageContentScroll)
  }
})
document.addEventListener('touchmove', function (event) {
  if (target) {
    if (!(event.touches.length === 1 && checkOffset(event.touches[0]))) {
      target.classList.remove('active-state')
      target = null
    }

    // 如果鼠标移除了link所在的范围，则会终止事件
  }
})
document.addEventListener('touchend', function (event) {
  if (target) {
    target.classList.remove('active-state')

    var currentPosition = getPosition()
    if (currentPosition.top !== offsetTop || currentPosition.left !== offsetLeft) return
      // 计算是否存在滚动

    var currentTarget = target
    var currentDir

    while (currentTarget) {
      currentDir = currentTarget.onTap
      currentDir.handler()
      if (currentDir.bubbles === true) {
        currentTarget = getTarget(currentTarget.parentNode)
      } else {
        currentTarget = null
        currentDir = null
      }
    }

    setTimeout(() => {
      if (target) target.classList.remove('active-state')
      target = null
    }, 100)

    // android上需要延迟才会出现active-state
  }
})

// 如果pageContent发生了滚动，则会终止事件
var pageContentScroll = function () {
  this.removeEventListener('scroll', pageContentScroll)
  if (target) {
    target.classList.remove('active-state')
    target = null
  }
}

var getPageContent = function (target, query) {
  while (target) {
    if (target.className.indexOf('page-content') !== -1) {
      break
    } else {
      if (target === document.body) {
        target = null
      } else {
        target = target.parentNode
      }
    }
  }
  return target
}

// 获取tap节点
var getTarget = function (target) {
  while (target) {
    if (target.onTap) {
      break
    } else {
      if (target === document.body) {
        target = null
      } else {
        target = target.parentNode
      }
    }
  }
  return target
}

// 获取对象在屏幕的位置
var getPosition = function () {
  var offsetTop = 0
  var offsetLeft = 0

  var offsetParent = target
  while (offsetParent) {
    offsetTop += offsetParent.offsetTop - offsetParent.scrollTop
    offsetLeft += offsetParent.offsetLeft - offsetParent.scrollLeft
    if (offsetParent === document.body) {
      offsetParent = null
    } else {
      offsetParent = offsetParent.offsetParent
    }
  }
  return { top: offsetTop, left: offsetLeft }
}

var calcOffset = function () {
  var position = getPosition()

  offsetLeft = position.left
  offsetTop = position.top
  offsetRight = offsetLeft + target.offsetWidth
  offsetBottom = offsetTop + target.offsetHeight

  var margin = target.onTap.margin

  if (margin) {
    offsetTop -= margin
    offsetLeft -= margin
    offsetRight += margin
    offsetBottom += margin
  }
  // 如果为link，则会出发路由事件
}

var checkOffset = function (touch) {
  if (offsetTop < touch.clientY && touch.clientY < offsetBottom && offsetLeft < touch.clientX && touch.clientX < offsetRight) {
    return true
  } else {
    return false
  }
}
export default {
  acceptStatement: true,
  bind: function () {
    this.el.onTap = this
  },
  unbind: function () {
    delete this.el.onTap
  },
  update: function (handler) {
    if (typeof handler !== 'function') {
      Vue.config.devtools && Vue.util.warn('v-tap="' + this.expression + '" expects a function value, ' + 'got ' + handler, this.vm)
      return
    }
    if (this.arg) this.margin = parseInt(this.arg)
    if (this.modifiers.bubbles) this.bubbles = true
    this.handler = handler
  }
}



