<template>
  <div v-el:container class="canvas-image">
    <img :src="image" @load="imgLoadFinish" v-el:image>
    <canvas @touchstart="isTween=false" v-el:canvas></canvas>
  </div>
</template>
<script>
import Hammer from 'hammerjs'
export default {
  props: ['image', 'query'],
  data: function () {
    return {
      hammer: undefined,
      isPan: false, // 拖拽触发时为true
      isPinch: false, // 缩放触发时为true
      isTween: true, // 拖拽结束之后为true，直到缓动结束后为false
      tweenStagger: null // 用于记录缓动速度
    }
  },
  methods: {
    range: function (num, min, max) {
      if (min > max) {
        return Math.max(Math.min(num, min), max)
      } else {
        return Math.min(Math.max(num, min), max)
      }
      // 区间计算
    },
    imgLoadFinish: function () {
      this.size[10] = this.$els.image.width
      this.size[11] = this.$els.image.height

      if (this.option.style === 'fit') {
        this.translate[2] = Math.min(this.size[0] / this.size[10], this.size[1] / this.size[11])
      } // 留空
      if (this.option.style === 'fill') {
        this.translate[2] = Math.max(this.size[0] / this.size[10], this.size[1] / this.size[11])
      } // 铺满
      // 计算初始缩放
      this.translate[0] = (this.size[0] - this.translate[2] * this.size[10]) / 2
      this.translate[1] = (this.size[1] - this.translate[2] * this.size[11]) / 2
        // 居中
      this.translate[20] = (this.size[10] + this.size[0]) / 2
      this.translate[21] = (this.size[11] + this.size[1]) / 2
      this.translate[22] = this.translate[2]
        // 计算最小值
      this.animate()
    },
    animate: function () {
      if (this.isTween) {
        // 拖动结束之后会进入缓动状态
        if (!this.tweenStagger) {
          // tweenStagger为空时代表tween开始
          var current = this.historyTranslateCurrent * 2
          if (this.historyTranslate.length === current) {
            current = 0
          }
          // 如果指针超出数组界限，会取第一项
          this.tweenStagger = [
            (this.translate[0] - this.historyTranslate[current + 0]) / (this.historyTranslate.length / 2 - 2), (this.translate[1] - this.historyTranslate[current + 1]) / (this.historyTranslate.length / 2 - 2)
          ]

          // 取出所有历史纪录，计算速度差
          if (isNaN(this.tweenStagger[0]) || this.tweenStagger[0] === Infinity || this.tweenStagger[0] === -Infinity) this.tweenStagger[0] = 0
          if (isNaN(this.tweenStagger[1]) || this.tweenStagger[1] === Infinity || this.tweenStagger[1] === -Infinity) this.tweenStagger[1] = 0
            // 避免计算错误导致的黑屏事件 此处有bug，但是非常不明显，使用兼容方案
            // 如果历史长度刚好为4，则可能会出现无穷大的问题，需要修复
        }
        this.translate[0] += this.tweenStagger[0] = this.tweenStagger[0] * 0.9625
        this.translate[1] += this.tweenStagger[1] = this.tweenStagger[1] * 0.9625
          // 每帧减速的速度
        if (Math.abs(this.tweenStagger[0]) < 0.25 && Math.abs(this.tweenStagger[1]) < 0.25) {
          this.tweenStagger = null
          this.isTween = false
        }
        // 如果缓动过小，直接忽略
      }
      if (!(this.translate[0] === this.translate[30] && this.translate[1] === this.translate[31] && this.translate[2] === this.translate[32])) {
        // 没有变化时直接跳过
        this.translate[0] = this.range(this.translate[0], -(this.size[10] * this.translate[2] - this.size[0]), 0)
        this.translate[1] = this.range(this.translate[1], -(this.size[11] * this.translate[2] - this.size[1]), 0)
          // 检测是否超出界限
        if (this.translate[30] === this.translate[0] && this.translate[31] === this.translate[1]) {
          if (this.isTween) {
            this.isTween = false
          }
        }
        // 如果已经达到边界 停止缓动
        this.translate[30] = this.translate[0]
        this.translate[31] = this.translate[1]
        this.translate[32] = this.translate[2]

        var arr = [
          this.translate[0], this.translate[1],
          this.size[10] * this.translate[2], this.size[11] * this.translate[2]
        ]
        this.context.clearRect(0, 0, this.size[0], this.size[1])
        this.context.drawImage(this.$els.image, arr[0], arr[1], arr[2], arr[3])
      }

      this.historyTranslate[2 * this.historyTranslateCurrent + 0] = this.translate[30]
      this.historyTranslate[2 * this.historyTranslateCurrent + 1] = this.translate[31]
      this.historyTranslateCurrent += 1
      if (this.historyTranslateCurrent > 9) this.historyTranslateCurrent -= 10
        // 将滚动添加到历史纪录 并且修改索引，最大只能存放10条历史

      if (this.isTween || this.isPan || this.isPinch) {
        window.requestAnimationFrame(this.animate)
          // 这个就是那个该死的动画调用堆栈
      }
    }
  },
  ready: function () {
    this.option = this.option || {}
    this.option.style = this.option.style || 'fill'

    this.$els.canvas.width = this.$els.container.offsetWidth
    this.$els.canvas.height = this.$els.container.offsetHeight

    this.context = this.$els.canvas.getContext('2d')

    this.size = [this.$els.container.offsetWidth, this.$els.container.offsetHeight]
      // 0x width,height $el
      // 1x width,height img

    this.translate = [0, 0, 1]
      // 0x x,y,scale 当前值
      // 1x x,y,scale 初始值
      // 2x x,y,scale 最小值
      // 3x x,y,scale 上一帧

    this.historyTranslateCurrent = 0
    this.historyTranslate = []

    this.hammer = new Hammer(this.$els.container)

    this.hammer.get('pinch').set({
      enable: true
    })
    this.hammer.get('tap').set({
      enable: false
    })
    this.hammer.get('pan').set({
      threshold: 1
    })
    this.hammer.on('panstart', (event) => {
      this.isPan = true
      this.isTween = false
      this.translate[10] = this.translate[0] - event.deltaX
      this.translate[11] = this.translate[1] - event.deltaY
        // 防止缩放之后再移动出现的跳动
      this.historyTranslateCurrent = 0
      this.historyTranslate = []

      this.animate()
    })
    this.hammer.on('panend', (event) => {
      this.isPan = false
      this.isTween = true
      this.tweenStagger = null
    })
    this.hammer.on('pan', (event) => {
      this.translate[0] = this.translate[10] + event.deltaX
      this.translate[1] = this.translate[11] + event.deltaY
        // 调整移动量时需要把缩放计算在内
    })
    this.hammer.on('pinchstart', (event) => {
      this.isPinch = true
      this.isTween = false
      this.translate[10] = this.translate[0]
      this.translate[11] = this.translate[1]
      this.translate[12] = this.translate[2]
        // 由于pinch的触发距离为0所以不会出现跳动
      this.animate()
    })
    this.hammer.on('pinchend', (event) => {
      this.isPinch = false
      this.isTween = false
    })
    this.hammer.on('pinch', (event) => {
      this.translate[2] = Math.max(this.translate[12] * event.scale, this.translate[22])
        // 防止scale超过最小值 由于scale涉及到的地方很多，所以一开始就进行限定
      this.translate[0] = event.deltaX - ((this.translate[2] / this.translate[12]) * (-this.translate[10] + event.center.x) - event.center.x)
      this.translate[1] = event.deltaY - ((this.translate[2] / this.translate[12]) * (-this.translate[11] + event.center.y) - event.center.y)
        // 调整移动量时需要把缩放计算在内
    })
  },
  beforeDestroy: function () {
    this.translate = null
    this.size = null
    this.hammer.destroy()
    this.hammer = undefined
  }
}
</script>
<style>
.canvas-image {
  height: 100%;
  width: 100%
}

.canvas-image img {
  display: none;
}

.canvas-image .canvas {
  display: block;
}
</style>



