<template>
  <div class="panel-overlay" v-tap="closePanel">
  </div>
</template>
<script>
import $panel from './../operates/$panel'
export default {
  props: ['index', 'query'],
  data: function () {
    return {}
  },
  methods: {
    closePanel: function () {
      $panel.close()
    }
  }
}
</script>
<style>
.panel-overlay {
  display: block;
}
</style>



