import $view from './$view'
import Vue from 'vue'
setTimeout(function () {
  window.onpopstate = function (event) {
    $view.back()
  }
}, 1000)
export default {
  start: function () {
    if (window.location.pathname !== '/') {
      var param = window.location.pathname.slice(1).split('/')
      window.history.replaceState({}, ',', '/')
      Vue.nextTick(function () {
        window.history.pushState(param[2], ',', '/' + param[0] + (param[1] ? ('/' + '' + param[1]) : ''))
        $view.load(param[0], param[1], param[2])
      })
    } else {
      if (window.location.search !== '') {
        Vue.nextTick(function () {
          // window.history.replaceState({}, ',', '/')

          // 防止加载初始页面是出现返回无效的问题
          // 防止url中参数一直存在的问题
        })
      }
    }

    // 为了在任何时候可以返回到主页，在路由开始之前先用主页替换掉路由也，再追加路由页
  },
  back: function () {
    window.history.back()
  },
  go: function (comp, id, query) {
    window.history.pushState(query, ',', '/' + comp + (id ? ('/' + '' + id) : ''))
    $view.load(comp, id, query)
  }
}



