var view
export default {
  load: function (comp, id, query) {
    if (comp) view.load(comp, id, query)
  },
  back: function () {
    view.back()
  },
  clear: function () {
    view.clear()
  },
  init: function (value) {
    if (!view) view = value
  }
}



