<template>
  <page :index="index">
    <canvas-image :image="mapSrc"></canvas-image>
    <speed-dial :query="menuQuery"></speed-dial>
  </page>
</template>
<script>
const unloginMenu = function () {
  return {
    $tag: 'userHome',
    svg: '<svg fill="#FFFFFF" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M12 5.9c1.16 0 2.1.94 2.1 2.1s-.94 2.1-2.1 2.1S9.9 9.16 9.9 8s.94-2.1 2.1-2.1m0 9c2.97 0 6.1 1.46 6.1 2.1v1.1H5.9V17c0-.64 3.13-2.1 6.1-2.1M12 4C9.79 4 8 5.79 8 8s1.79 4 4 4 4-1.79 4-4-1.79-4-4-4zm0 9c-2.67 0-8 1.34-8 4v3h16v-3c0-2.66-5.33-4-8-4z"/><path d="M0 0h24v24H0z" fill="none"/></svg>',
    onClick: function () {
      window.location = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxf7129b56ed54f871&redirect_uri=http%3A%2F%2F' + window.location.host + '&response_type=code&scope=snsapi_userinfo&state=1123#wechat_redirect'
    }
  }
}
const loginMenu = function () {
  return {
    $tag: 'userHome',
    img: window.vm.user.head,
    link: ['UserHome']
  }
}
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      mapSrc: 'http://192.168.1.11/canvasImage.png',
      menuQuery: [{
        svg: '<svg fill="#FFFFFF" height="20" viewBox="0 0 24 24" width="20" xmlns="http://www.w3.org/2000/svg"><path d="M3 17.25V21h3.75L17.81 9.94l-3.75-3.75L3 17.25zM20.71 7.04c.39-.39.39-1.02 0-1.41l-2.34-2.34c-.39-.39-1.02-.39-1.41 0l-1.83 1.83 3.75 3.75 1.83-1.83z"/><path d="M0 0h24v24H0z" fill="none"/></svg>',
        onClick: function () {
          window.vm.$v7.$popup.open('Publish')
        }
      }, {
        svg: '<svg fill="#FFFFFF" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 22c1.1 0 2-.9 2-2h-4c0 1.1.9 2 2 2zm6-6v-5c0-3.07-1.63-5.64-4.5-6.32V4c0-.83-.67-1.5-1.5-1.5s-1.5.67-1.5 1.5v.68C7.64 5.36 6 7.92 6 11v5l-2 2v1h16v-1l-2-2zm-2 1H8v-6c0-2.48 1.51-4.5 4-4.5s4 2.02 4 4.5v6z"/></svg>',
        link: ['Information']
      }, {
        svg: '<svg fill="#FFFFFF" height="22" viewBox="0 0 24 24" width="22" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h24v24H0z" fill="none"></path><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm3 4H8c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h11c1.1 0 2-.9 2-2V7c0-1.1-.9-2-2-2zm0 16H8V7h11v14z"></path></svg>',
        link: ['List']
      }]
    }
  },
  methods: {
    click: function () {
      this.$v7.$router.go('SvgMap')
    }
  },
  watch: {
    '$root.isLogin': function (newValue) {
      this.menuQuery.delete(function (item) {
        return item.$tag === 'userHome'
      })
      if (newValue) {
        this.menuQuery.push(loginMenu())
      } else {
        this.menuQuery.push(unloginMenu())
      }
    }
  },
  ready: function () {
    if (this.$root.isLogin) {
      this.menuQuery.push(loginMenu())
    } else {
      this.menuQuery.push(unloginMenu())
    }
  }
}
</script>
<style>
</style>



