<template>
  <page :index="index">
    <navbar :index="index" title="信息中心"></navbar>
    <page-list url="notice/show" property="notice" :data.sync="data" :list.sync="list" :post-data="">
      <div class="list-block media-list" style="margin: 0;">
        <ul>
          <li v-for='part in list'>
            <div class="item-content" v-if="part.type=='comment'">
              <div class="item-media" style="background: rgba(0,0,0,.22);padding: 0">
                <img src="http://www.wozaizheer.cn/picture/spot/seller.png" style="width: 50px;border-radius: 100%;">
              </div>
              <div class="item-inner">
                <div class="item-title-row">
                  <div class="item-title" v-text="part.user.name"></div>
                  <div class="item-after" style="font-size: 14px" v-dateformat="part.create_at"></div>
                </div>
                <div class="item-subtitle">评论：<span v-text="part.option.comment.content"></span></div>
              </div>
            </div>
            <div class="item-content" v-if="part.type=='reply'">
              <div class="item-media" style="background: rgba(0,0,0,.22);padding: 0">
                <img src="http://www.wozaizheer.cn/picture/spot/seller.png" style="width: 50px;border-radius: 100%;">
              </div>
              <div class="item-inner">
                <div class="item-title-row">
                  <div class="item-title" v-text="part.user.name"></div>
                  <div class="item-after" style="font-size: 14px" v-dateformat="part.create_at"></div>
                </div>
                <div class="item-subtitle">回复：<span v-text="part.option.reply.content"></span></div>
              </div>
            </div>
            <div class="item-content" v-if="part.type=='like'">
              <div class="item-media" style="background: rgba(0,0,0,.22);padding: 0">
                <img src="http://www.wozaizheer.cn/picture/spot/seller.png" style="width: 50px;border-radius: 100%;">
              </div>
              <div class="item-inner">
                <div class="item-title-row">
                  <div class="item-title" v-text="part.user.name"></div>
                  <div class="item-after" style="font-size: 14px" v-dateformat="part.create_at"></div>
                </div>
                <div class="item-subtitle">赞了你一下</div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </page-list>
  </page>
</template>
<script>
export default {
  props: ['index', 'id', 'query'],
  data: function () {
    return {
      data: null,
      list: []
    }
  },
  methods: {},
  ready: function () {
    this.$nextTick(function () {
      console.log(this.data)
      console.log(this.list)
    })
  }
}
</script>



