<template>
  <div class="modal" v-show="option.show" transition="modal" :class="{'modal-no-buttons':!query.buttons || query.buttons.length==0}">
    <div class="modal-inner">
      <div class="modal-title" v-text="query.title || $root.params.modalTitle"></div>
      <div class="modal-text" v-if="query.text" v-html="query.text"></div>
      <div v-if="query.afterText" v-html="query.afterText"></div>
      <component :is="query.afterComponent"></component>
    </div>
    <div class="modal-buttons" v-if="query.buttons" :class="{'modal-buttons-vertical':query.verticalButtons}">
      <span v-for="button in query.buttons" @click="closeModal($index)" class="modal-button" :class="{'modal-button-bold':button.bold}" v-text="button.text"></span>
    </div>
  </div>
</template>
<script>
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        show: true
      }
    }
  },
  methods: {
    closeModal: function (index) {
      this.$root.bodyChildren.splice(this.index, 1)
      if (this.query.buttons[index].onClick) this.query.buttons[index].onClick(this.$el)
      if (this.query.onClick) this.query.onClick(this.$el, index)
    }
  },
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'ModalOverlay'
    })
  },
  ready: function () {
    this.$el.style.marginTop = -Math.round(this.$el.offsetHeight / 2) + 'px'
  },
  beforeDestroy: function () {
    this.$root.bodyChildren.delete(function (item) {
      return item.comp === 'ModalOverlay'
    })
  }
}
</script>
<style>
.modal.modal-transition {
  display: block;
  -webkit-transition-duration: 400ms;
  transition-duration: 400ms;
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) scale(1);
  transform: translate3d(0, 0, 0) scale(1);
}

.modal.modal-enter {
  opacity: 0;
  -webkit-transform: translate3d(0, 0, 0) scale(1.185);
  transform: translate3d(0, 0, 0) scale(1.185);
}

.modal.modal-leave {
  opacity: 0;
}
</style>



