export default {
  show: function (query) {
    window.vm.bodyChildren.push({
      comp: 'Morph',
      query: query
    })
  },
  hide: function () {
    window.vm.bodyChildren.delete(function (item) {
      return item.comp === 'Morph'
    })
  }
}



