<template>
  <div class="popup" transition="popup">
    <slot></slot>
  </div>
</template>
<script>
export default {
  props: ['index'],
  data: function () {
    return {}
  },
  methods: {},
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'PopupOverlay'
    })

    this.onpopstate = window.onpopstate
    window.history.pushState({}, ',', document.location.pathname)
    window.onpopstate = (event) => {
      this.$v7.$popup.close()
    }

    // 响应浏览器的back，由于popup不需要参与到转发中 所以需要维持之前的路由不变
  },
  beforeDestroy: function () {
    this.$root.bodyChildren.delete(function (item) {
      return item.comp === 'PopupOverlay'
    })
    window.onpopstate = this.onpopstate
    delete this.onpopstate

    // 清空于路由相关的内容
  }
}
</script>
<style>
.popup.popup-transition {
  display: block;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  transition-duration: 400ms;
}

.popup.popup-enter,
.popup.popup-leave {
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}
</style>



