import Vue from 'vue'
import F7 from './Framework7'
Vue.use(F7)

import VueResource from 'vue-resource'
Vue.use(VueResource)
Vue.http.options.xhr = {
  withCredentials: true
}

Vue.util.extend(Vue.prototype, {
  // apiUrl: 'http://api.qumaopao.com/',
  apiUrl: 'http://192.168.1.11:3000/',
  staticUrl: 'http://static.qumaopao.com/'
})

Vue.http.interceptors.push({
  response: function (res) {
    if (res.data.isError) {
      res.ok = false
    }
    if (res.status === 0) {
      res.data = {}
      res.data.isError = true
      res.data.message = '您似乎已断开与互联网的连接'
    }
    return res
  }
})

import io from 'socket.io-client'
import WeiXin from './plugin/weixin'
WeiXin

import CanvasImage from './components/CanvasImage'
import Floor from './components/Floor'
import SvgMap from './components/SvgMap'
import Map from './components/Map'
import SpeedDial from './components/SpeedDial'
import Menu from './components/Menu'
import UserHome from './components/UserHome'
import UserInfo from './components/UserInfo'
import UserHead from './components/UserHead'
import Publish from './components/Publish'
import Information from './components/Information'
import List from './components/List'
import Bubble from './components/Bubble'
import Comment from './components/Comment'
import Gallery from './components/Gallery'
import StatusCard from './components/StatusCard'

import PullRefresh from './Framework7/components/PullRefresh'
import InfiniteScroll from './Framework7/components/InfiniteScroll'
import Popup from './Framework7/components/Popup'
import UploadItem from './Framework7/components/UploadItem'

import dateformat from './directives/dateformat'

Vue.component('CanvasImage', CanvasImage)
Vue.component('Floor', Floor)
Vue.component('SvgMap', SvgMap)
Vue.component('Map', Map)
Vue.component('SpeedDial', SpeedDial)
Vue.component('Menu', Menu)
Vue.component('UserHome', UserHome)
Vue.component('UserInfo', UserInfo)
Vue.component('UserHead', UserHead)
Vue.component('Publish', Publish)
Vue.component('Information', Information)
Vue.component('List', List)
Vue.component('Bubble', Bubble)
Vue.component('Comment', Comment)
Vue.component('Gallery', Gallery)
Vue.component('StatusCard', StatusCard)

Vue.component('PullRefresh', PullRefresh)
Vue.component('InfiniteScroll', InfiniteScroll)
Vue.component('Popup', Popup)
Vue.component('UploadItem', UploadItem)

Vue.directive('dateformat', dateformat)

Vue.prototype.$util = Vue.util

var vm = new Vue({
  el: 'body',
  data: {
    bodyChildren: [{
      comp: 'Menu'
    }, {
      comp: 'View'
    }],
    isLogin: false,
    user: undefined,
    tapAction: true
  },
  methods: {
    notify: function (msg) {
      this.$v7.$notifications.add({
        title: '去冒泡',
        message: msg
      })
    },
    socketIo: function () {
      // var socket = io('http://192.168.1.11:1337/notice')
      var socket = io(this.apiUrl + 'notice')
      socket.on('notice', (msg) => {
        switch (msg.type) {
          case 'comment':
            this.notify(msg.user.name + '评论了你')
            break
          case 'reply':
            this.notify(msg.user.name + '回复了你')
            break
          case 'like':
            this.notify(msg.user.name + '点赞了你')
            break
          case 'friendRequest':
            this.notify(msg.user.name + '申请加你为好友')
            break
          case 'acceptFriendRequest':
            this.notify(msg.user.name + '同意加你为好友')
            break
          case 'refuseFriendRequest':
            this.notify(msg.user.name + '拒绝加你为好友')
            break
        }
      })
    }
  },
  watch: {
    user: function (newValue) {
      if (newValue !== undefined) {
        this.isLogin = true
        vm.socketIo()
      } else {
        this.isLogin = false
      }
    }
  }
})

if (window.location.search !== '' && window.location.search.indexOf('code=') !== -1) {
  let code = window.location.search.match('code=([^&]*)')[1]
  vm.$http.get(vm.apiUrl + 'account/wechatLogin', {
    code: code
  }).then(function (response) {
    vm.$http.get(vm.apiUrl + 'user/info').then(function (response) {
      this.user = response.data
    }, function (response) {
      console.log(response)
    })
  }, function (response) {})
} else {
  vm.$http.get(vm.apiUrl + 'user/info').then(function (response) {
    this.user = response.data
  }, function (response) {
    console.log(response)
  })
}

Vue.transition('fade-rl', {
  type: 'animation'
})

vm.$v7.$view.load('Map')

vm.$v7.$router.start()

window.vm = vm
window.F7 = F7



