var comment
export default {
  /*
   * @param   {
   *            direction:  'dowm' / 'up' / 'scroll'
   *          }
   */
  toggleScrollToolbars: function (query) {
    if (comment) {
      if (query.direction === 'down') {
        comment.status = true
      } else if (query.direction === 'up') {
        comment.status = false
      } else if (query.direction === 'scroll') {
        this.hide()
      }
    }
  },
  comment: function (query) {
    if (comment) {
      comment.$data.commentid = query.commentId
      comment.status = query.hideComment || ''
    }
  },
  show: function (query) {
    if (comment) comment.status = false
  },
  hide: function (query) {
    if (comment) comment.status = true
  },
  init: function (Comment) {
    comment = Comment
  }
}



