<template>
  <!-- 未审核 -->
  <div class="morph popover popover-contacts popover-floating-button modal-in popover-on-top" :style="'left:'+option.pop_left+'px;top:'+option.pop_top+'px;'" transition="morph">
    <div class="popover-inner">
      <div class="list-block media-list">
        <ul>
          <!-- <li class="item-content" v-for="item in query.items" v-if="query.items">
            <div class="item-media" v-html="item.mediaHTML" v-if="item.mediaHTML"></div>
            <div class="item-media" v-if="!item.mediaHTML">
              <img :src="item.img" width="40" v-if="item.img">
            </div>
            <div class="item-inner">
              <div class="item-title-row" v-html="item.itemHTML" v-if="item.itemHTML"></div>
              <div class="item-title-row" v-if="!item.itemHTML">
                <div class="item-title" v-tap="click(item)" v-text="item.title"></div>
              </div>
            </div>
          </li> -->
          <li class="item-content" v-tap="click(0)">
            <div class="item-media">
              <img src="http://192.168.1.11/canvasImage.jpg" style="width: 40px; height: 40px; border-radius: 50%;">
            </div>
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title" v-text="'Allen Iverson'"></div>
              </div>
              <div class="item-subtitle">男</div>
            </div>
          </li>
          <li class="item-content" v-tap="click(1)">
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title" v-text="'列表'"></div>
              </div>
            </div>
          </li>
          <li class="item-content" v-tap="click(2)">
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title" v-text="'消息'"></div>
              </div>
            </div>
          </li>
          <li class="item-content" v-tap="click(3)">
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title" v-text="'冒泡'"></div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import F7 from './../../Framework7'
import $morph from './../operates/$morph'
export default {
  props: ['index', 'query'],
  data: function () {
    return {
      option: {
        pop_left: 0,
        pop_top: 0
      }
    }
  },
  methods: {
    sizePopover: function () {
      var target = window.event.target
      this.target = target
      var morphObj = document.getElementsByClassName('morph')[0]
      var modalWidth = morphObj.offsetWidth
      var modalHeight = morphObj.offsetHeight
      var targetWidth = target.offsetWidth
      var targetHeight = target.offsetHeight
      var windowWidth = document.body.offsetWidth
      var windowHeight = document.body.offsetHeight
      var targetTop = target.offsetTop
      var targetLeft = target.offsetLeft
      var modalTop = 0
      var modalLeft = 0
        // var flag = false

      if (modalHeight < targetTop) {
        modalTop = targetTop - modalHeight + targetHeight
      } else if (modalHeight < windowHeight - targetTop - targetHeight) {
        modalTop = targetTop + targetHeight
      }
      modalLeft = targetWidth / 2 + targetLeft - modalWidth / 2
      if (modalLeft < 8) modalLeft = 8
      if (modalLeft + modalWidth > windowWidth) modalLeft = windowWidth - modalWidth - 8

      var diffX = (modalLeft + modalWidth / 2) - (targetLeft + targetWidth / 2)
      var diffY = (modalTop + modalHeight / 2) - (targetTop + targetHeight / 2)
      target.style.transform = 'translate3d(' + diffX + 'px, ' + diffY + 'px,0)'
      target.style.opacity = '0'
        // target.addEventListener('transitionend', function () {
        //   if (flag) return
        //   target.classList.add('floating-button-to-popover-scale')
        //   target.style.transform = 'translate3d(' + diffX + 'px, ' + diffY + 'px,0) scale(' + (modalWidth / targetWidth) + ', ' + (modalHeight / targetHeight) + ')'
        //   flag = true
        // })
        // target.addEventListener('webkitTransitionEnd', function () {
        //   if (flag) return
        //   target.classList.add('floating-button-to-popover-scale')
        //   target.style.transform = 'translate3d(' + diffX + 'px, ' + diffY + 'px,0) scale(' + (modalWidth / targetWidth) + ', ' + (modalHeight / targetHeight) + ')'
        //   flag = true
        // })

      this.option.pop_top = modalTop
      this.option.pop_left = modalLeft
    },
    closePopover: function () {
      // this.target.classList.remove('floating-button-to-popover-scale')
      this.target.style.opacity = '1'
      this.target.style.transform = ''
    },
    click: function (item) {
      // if (item.onClick) item.onClick()
      $morph.hide()
      if (item === 0) {
        console.log(item)
        F7.operates.$router.go('UserHome')
          // F7.operates.$view.load('Publish')
      } else if (item === 1) {
        console.log(item)
        F7.operates.$router.go('List')
      } else if (item === 2) {
        console.log(item)
        F7.operates.$router.go('Information')
      } else if (item === 3) {
        console.log(item)
        F7.operates.$router.go('Publish')
      }
    }
  },
  attached: function () {
    this.sizePopover()
  },
  compiled: function () {
    this.$root.bodyChildren.push({
      comp: 'MorphOverlay'
    })
  },
  beforeDestroy: function () {
    this.$root.bodyChildren.delete(function (item) {
      return item.comp === 'MorphOverlay'
    })
    this.closePopover()
  }
}
</script>
<style>
.morph.morph-transition {
  display: block;
  border-radius: 0%;
  width: auto;
  opacity: 1;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .16), 0 3px 6px rgba(0, 0, 0, .23);
  -webkit-transition-property: opacity, -webkit-transform, border-radius;
  -moz-transition-property: opacity, -moz-transform, border-radius;
  transition-property: opacity, transform, border-radius;
  -webkit-transition-delay: 200ms;
  transition-delay: 200ms;
  -webkit-transition-duration: 300ms;
  transition-duration: 300ms;
}

.morph.morph-enter {
  border-radius: 100%;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.morph.morph-leave {
  opacity: 0;
  border-radius: 50%;
  -webkit-transition-delay: 0ms;
  transition-delay: 0ms;
  -webkit-transition-duration: 100ms;
  transition-duration: 100ms;
}
</style>



