var m = 60 * 1000
var h = 60 * m
var d = 24 * h
var nowDays = Math.floor(new Date().getTime() / d)
var nowYear = new Date().getFullYear()

var toAutoDateString = function (date) {
  var offset = nowDays - Math.floor(date.getTime() / d)
  if (offset === 0) {
    return '今天'
  } else {
    if (offset === 1) {
      return '昨天'
    } else if (offset === 2) {
      return '前天'
    } else {
      return (date.getFullYear() === nowYear ? '' : date.getFullYear() + ' ') + toFullMouth(date.getMonth()) + '-' + toFullDay(date.getDate())
    }
  }
}

var toFullMouth = function (month) {
  month += 1
  return month >= 9 ? month.toString() : '0' + month.toString()
}

var toFullMinute = function (minute) {
  return minute > 9 ? minute.toString() : '0' + minute.toString()
}

var toFullDay = function (day) {
  return day > 9 ? day.toString() : '0' + day.toString()
}

var toAutoString = function (date) {
  if (date.getFullYear() === 1) {
    return '---- -----' // 如果时间为空则返回空字符串
  }
  var offset = new Date().getTime() - date.getTime()
  if (offset < m) {
    return '刚刚'
  }
  if (offset < h) {
    return (offset / m).toFixed(0) + ' 分钟前'
  }
  if (date.hours === 0 && date.minutes === 0 && date.seconds === 0) {
    return toAutoDateString(date)
  }
  return toAutoDateString(date) + (date.getFullYear() === nowYear ? ' ' + date.getHours() + ':' + toFullMinute(date.getMinutes()) : '')
}

export default function (date) {
  this.el.innerHTML = toAutoString(new Date(Date.parse(date)))
}



