import Hammer from 'hammerjs'
import Vue from 'vue'
const transformProp = (document.body.style.transform === undefined && document.body.style.webkitTransform !== undefined) ? 'webkitTransform' : 'transform'

export default {
  bind: function () {
    Vue.nextTick(() => {
      if (this.vm.$refs.pullRefresh) {
        this.el.classList.add('pull-to-refresh-no-navbar', 'pull-to-refresh-content')
        this.pullHeight = this.vm.$refs.pullRefresh.$el.offsetHeight
        this.hammer = new Hammer(this.el)

        // this.hammer.get('pan').set({
        //   direction: Hammer.DIRECTION_VERTICAL,
        //   threshold: 10
        // })

        this.el.style.touchAction = ''

        this.isPull = false
          // 是否响应下拉，用于防止正在loading的时候被下拉
        this.isRefresh = false
          // 是否达到refresh，放手时会触发刷新
        this.isPulling = false
          // pagecontent是否已经下拉
        this.isRefreshing = false
          // 是否正在处于刷新状态
        this.startDelta = 0
          // 用于防止下拉时10px的跳动
        this.startScrollTop = 0
          // 用于防止scrollTop不为0时下拉跳动

        this.vm.$on('refreshed', this.refreshed = (e) => {
          this.el.classList.remove('pull-up', 'refreshing')
          setTimeout(() => {
            this.el.classList.remove('transitioning')
            this.isRefreshing = false
          }, 400)
        })

        this.hammer.on('panstart', (e) => {
          if (!this.isRefreshing) {
            this.el.classList.remove('transitioning')
            this.el.classList.add('pull-down')
            this.startDelta = e.deltaY

            this.startScrollTop = this.el.scrollTop
              // 用于防止scrollTop不为0时下拉跳动
            this.isPull = true
          } else {
            this.isPull = false
          }
        })

        this.hammer.on('panmove', (e) => {
          if (this.isPull && this.el.scrollTop === 0) {
            // 只有下拉，以及已经滚动到顶部才会出现触发刷新
            var delta = (Math.pow(e.deltaY, 0.85) - this.startDelta - this.startScrollTop)
            if (delta <= 0) return
              // 只有下拉为正数才触发
            e.srcEvent.preventDefault()
              // 终止滚动事件
            this.el.style[transformProp] = 'translate3D(0,' + delta + 'px,0)'
            if (delta > this.pullHeight) {
              if (!this.isRefresh) {
                this.el.classList.add('pull-up')
                this.el.classList.remove('pull-down')
                this.isRefresh = true
              }
            } else {
              if (this.isRefresh) {
                this.el.classList.add('pull-down')
                this.el.classList.remove('pull-up')
                this.isRefresh = false
              }
            }

            this.isPulling = true
          } else {
            if (this.isPulling) {
              this.el.style[transformProp] = ''
              this.isPulling = false
            }
          }
        })

        this.hammer.on('panend', (e) => {
          this.el.classList.add('transitioning')
          this.el.style[transformProp] = ''
          if (this.isPulling && this.isRefresh) {
            this.el.classList.add('refreshing')
            this.isRefreshing = true
            this.vm.$dispatch('refreshing')
          } else {
            setTimeout(() => {
              this.el.classList.remove('pull-down')
            }, 400)
          }
          this.isPulling = false
          this.isPull = false
        })
      }

      if (this.vm.$refs.infiniteScroll) {
        this.infiniteHeight = this.vm.$refs.infiniteScroll.$el.offsetHeight
        this.scrolling = false

        this.vm.$on('scrolled', this.refreshed = (e) => {
          this.scrolling = false
          this.el.classList.remove('scrolling')
        })

        this.scrollHandler = (e) => {
          if (this.scrolling === false && !this.el.classList.contains('no-more') && this.el.scrollHeight - (this.el.scrollTop + this.el.offsetHeight * 2) <= 0) {
            this.el.classList.add('scrolling')
            this.scrolling = true
            this.vm.$dispatch('scrolling')
          }
        }

        this.el.addEventListener('scroll', this.scrollHandler)
      }
    })
  },
  unbind: function () {
    this.el.removeEventListener('scroll', this.scrollHandler)
    delete this.scrollHandler
    this.hammer.destroy()
    delete this.hammer
  }
}



